import './EventCell.scss';

import React, { useMemo } from 'react';

import { intervalToStringLight } from '../../../../../../assembly/interval';
import { pickRandom } from '../../../../../../assembly/random';
import { Event } from '../../../../../../stores/events';
import GridCellWrapper from '../../GridCellWrapper';

export interface EventCellProps {
  event: Event;
  x: number;
  y: number;
  width: number;
  height: number;
}

