// const colors = ["#b88b8b", "#deadad", "#d6c7c7", "#9fb9be", "#aec8ce"];
const colors = ["yellow", "#FF957D", "#E5FF7C", "#FFD585"];

const avatars = [
  "data:image/png;base64,",
  "data:image/png;base64,",
  "data:image/png;base64,",
  "data:image/png;base64,",
];

const name2color: Record<string, string> = {};
const name2avatar: Record<string, string> = {};

const EventCell = ({ event, ...props }: EventCellProps) => {
  const { interval, name, userIds } = event;

  const color = useMemo(() => {
    if (!name2color[name]) {
      if (!colors.length) return "#fff2cc";

      name2color[name] = pickRandom(colors);
      colors.splice(colors.indexOf(name2color[name]), 1);
    }

    return name2color[name];
  }, [name]);

  const imgSrc = useMemo(() => {
    if (!name2avatar[name]) {
      if (!avatars.length) return "";

      name2avatar[name] = pickRandom(avatars);
      avatars.splice(avatars.indexOf(name2avatar[name]), 1);
    }

    return name2avatar[name];
  }, [name]);

  return (
    <GridCellWrapper
      {...props}
      data={{
        name,
        info: intervalToStringLight(interval),
        counter: event.block, // userIds.length,
        color,
        imgSrc,
      }}
    />
  );
};

export default React.memo(EventCell);
