import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsBackGridContainer = createContainer(() => {
  const [visibileHorizontalLines, setVisibileHorizontalLines] = useCookie(
    "visibileHorizontalLines",
    false
  );

  const visibileHorizontalLinesSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: visibileHorizontalLines,
    name: "Horizontal lines visibility",
    setChecked: setVisibileHorizontalLines,
  };

  return { visibileHorizontalLines, visibileHorizontalLinesSetting };
});
