// tslint:disable
const useIsChanged = (dependencies: any[]) => {
  const ref = useRef(dependencies);

  const changed = ref.current.some((v, i) => v !== dependencies[i]);
  ref.current = dependencies;

  return changed;
};
// tslint:enable

function useAppBlocksJSX() {
  const appBlocksRef = useRef<Record<string, JSX.Element>>({});
  const [appBlocks, setAppBlocks] = useState<JSX.Element[]>([]);

  const renderedBlocksRef = useRef<Record<string, AppointmentsBlock>>({});
  const renderedBlocksZeroAxisRef = useRef<Record<string, number>>({});
  const { draggingApp } = DraggingContainer.useContainer();

  // console.log({ appBlocks: appBlocks.length });

  const reset = () => {
    setAppBlocks([]);
    renderedBlocksRef.current = {};
  };

  const addBlock = useCallback(
    (blockJSX: JSX.Element, block: AppointmentsBlock, zeroAxis: number) => {
      // tslint:disable-next-line:no-object-mutation
      appBlocksRef.current[block.id] = blockJSX;
      // tslint:disable-next-line:no-object-mutation
      renderedBlocksRef.current[block.id] = block;
      renderedBlocksZeroAxisRef.current[block.id] = zeroAxis;

      setAppBlocks(Object.values(appBlocksRef.current));
    },
    []
  );

  const addBlocks = useCallback((arr: Array<[JSX.Element, string]>) => {
    arr.forEach(
      ([blockJSX, id]) =>
        // tslint:disable-next-line:no-object-mutation
        (appBlocksRef.current[id] = blockJSX)
    );

    setAppBlocks(Object.values(appBlocksRef.current));
  }, []);

  const cleanFarBlocks = useCallback(
    (
      viewInterval: Interval,
      zeroAxisActual: { current: number },
      clearDistanceScale = 1
    ) => {
      const clearDistance =
        (viewInterval.end.valueOf() - viewInterval.start.valueOf()) *
        clearDistanceScale;
      const amountWas = Object.keys(appBlocksRef.current).length;
      // tslint:disable-next-line:no-object-mutation
      appBlocksRef.current = Object.entries(appBlocksRef.current)
        .filter(([id, jsx]) => {
          if (draggingApp && draggingApp.blockId === id) {
            // console.log(`stay ${id} here`);
            return true;
          }

          if (
            renderedBlocksZeroAxisRef.current[id] !== zeroAxisActual.current
          ) {
            console.log("clear because of zeroAxis mismatch");
            return false;
          }
          const { interval } = (jsx.props as AppointmentBlockProps).appsBlock;
          const dists = [
            viewInterval.start.valueOf() - interval.start,
            viewInterval.end.valueOf() - interval.start,
            viewInterval.start.valueOf() - interval.end,
            viewInterval.end.valueOf() - interval.end
          ].map(d => Math.abs(d));

          const minDist = dists.reduce((min, d) => Math.min(min, d));
          const notToClear = minDist < clearDistance;
          // if (!notToClear) {
          //   console.log(
          //     `clear block ${intervalToString(
          //       interval
          //     )} \n because of minDist=${minDist} greater than clearDistance=${clearDistance}`
          //   );
          // }
          return notToClear;
        })
        .reduce(
          (acc: Record<string, JSX.Element>, [key, jsx]) => ({
            ...acc,
            [key]: jsx
          }),
          {}
        );

      const amountNow = Object.keys(appBlocksRef.current).length;

      if (amountWas !== amountNow) {
        setAppBlocks(Object.values(appBlocksRef.current));
        console.log(`${amountWas} => ${amountNow} (clear)`);
      }
    },
    [draggingApp]
  );

  return {
    appBlocks,
    addBlock,
    addBlocks,
    cleanFarBlocks,
    renderedBlocks: renderedBlocksRef.current,
    reset
  };
}

