import { useEffect, useLayoutEffect, useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { fetchAppointments } from '../../fetchers/fetchAppointments';
import { AppointmentsInfo, AppointmentUnassigned } from '../../stores/appointments';
import { ViewIntervalBufferedContainer } from './containers/ViewIntervalBufferedContainer';

function useAutoFetcherApps() {
  return;

  const { viewIntervalBuffered } = ViewIntervalBufferedContainer.useContainer();
  // const { oneDayDuration, gridStepDuration } = GridInfo.useContainer();
  const { addAppointments } = AppointmentsInfo.useContainer();

  const [nextPusingApps, setNextPusingApps] = useState<AppointmentUnassigned[]>(
    []
  );

  useLayoutEffect(() => {
    if (nextPusingApps.length) {
      // console.log(`add ${nextPusingApps.length} apps`);
      addAppointments(nextPusingApps);
      setNextPusingApps([]);
    }
  }, [
    nextPusingApps.length,
    addAppointments,
    nextPusingApps,
    setNextPusingApps
  ]);

  useMemo(async () => {
    const apps = await fetchAppointments(viewIntervalBuffered);

    if (apps.length) setNextPusingApps(apps);
  }, [viewIntervalBuffered, setNextPusingApps]);
}

export const AutoFetcherApps = createContainer(useAutoFetcherApps);
