import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingsAddAppButtonContainer } from './SettingsAddAppButtonContainer';
import { SettingsAppContainer } from './SettingsAppContainer';
import { SettingsBackGridContainer } from './SettingsBackGridContainer';
import { SettingsColumnContainer } from './SettingsColumnContainer';
import { SettingsDividersContainer } from './SettingsDivideresContainer';
import { SettingsDraggingContainer } from './SettingsDraggingContainer';
import { SettingsLevelsBlockContainer } from './SettingsLevelsBlockContainer';

export enum SettingType {
  Range,
  Checkbox,
}

export type SettingChunk =
  | {
      type: SettingType.Range;
      name: string;
      min: number;
      max: number;
      value: number;
      setNewValue: (value: number) => void;
    }
  | {
      type: SettingType.Checkbox;
      name: string;
      checked: boolean;
      disabled?: boolean;
      setChecked: (checked: boolean) => void;
    };

export const SettingsContainer = createContainer(() => {
  const { minColumnWidthSetting } = SettingsColumnContainer.useContainer();
  const {
    onDraggingApartSetting,
    onDraggingSetting,
  } = SettingsDraggingContainer.useContainer();
  const {
    visibilityAvatarSetting,
    visibilityVisitsSetting,
  } = SettingsAppContainer.useContainer();
  const {
    visibileHorizontalLinesSetting,
  } = SettingsBackGridContainer.useContainer();
  const {
    visibleLevelsBlockSetting,
  } = SettingsLevelsBlockContainer.useContainer();
  const { dividerUnderBlockSetting } = SettingsDividersContainer.useContainer();
  const {
    addAppButtonOnSetting,
  } = SettingsAddAppButtonContainer.useContainer();

  return {
    settings: [
      minColumnWidthSetting,
      onDraggingSetting,
      onDraggingApartSetting,
      visibilityAvatarSetting,
      visibilityVisitsSetting,
      visibileHorizontalLinesSetting,
      visibleLevelsBlockSetting,
      dividerUnderBlockSetting,
      addAppButtonOnSetting,
    ],
  };
});
