import './ScrollingLayer.scss';

import classnames from 'classnames';
import React, { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';

import { StickyController } from '../../../../assembly/stickyController';
import { ScrollControlContainer } from '../../containers/ScrollControlContainer';
import { TimeLineContainer } from '../../containers/TimeLineContainer';
import { UIScrollStateContainer } from '../../containers/UIScrollStateContainer';
import { ViewIntervalPixelsContainer } from '../../containers/ViewIntervalPixelsContainer';
import { ViewIntervalContainer } from '../../containers/ViewIntevalContainer';
import { WrapperContainer } from '../../containers/WrapperContainer';
import AddAppButton from './AddAppButton';
import AppointmentsPlacer from './AppointmentsPlacer';
import BackGrid from './BackGrid';
import EventsPlacer from './EventsPlacer';
import RestrictedZones from './RestrictedZones';
import TimeLine from './TimeLine';

const ScrollingLayer = () => {
  const { width } = WrapperContainer.useContainer();
  const {
    isScrollingInitialized,
    scrollJustReset,
  } = ViewIntervalContainer.useContainer();
  const {
    viewIntervalPixelsPending,
    isScrollingInProgress,
    viewIntervalPixelsWidth,
  } = ViewIntervalPixelsContainer.useContainer();
  const { timeLineSize } = TimeLineContainer.useContainer();
  const { scrollDuration } = ScrollControlContainer.useContainer();
  const {
    setIsScrollingUI,
    isScrollingUI,
  } = UIScrollStateContainer.useContainer();

  const [translateX, setTranslateX] = useState(
    -viewIntervalPixelsPending.start
  );
  const [translateXPrevious, setTranslateXPrevious] = useState(translateX);

  useEffect(() => {
    setTimeout(() => setTranslateX(-viewIntervalPixelsPending.start));
  }, [viewIntervalPixelsPending.start]);

  // const translateX = -viewIntervalPixelsPending.start;
  const transform = `translate3d(${translateX}px, ${0}px, ${0}px)`;

  useLayoutEffect(() => {
    if (isScrollingInitialized && isScrollingInProgress && !isScrollingUI)
      setIsScrollingUI(true);
  }, [
    isScrollingInitialized,
    isScrollingInProgress,
    isScrollingUI,
    setIsScrollingUI,
  ]);

  const topBlockContent = useMemo(() => <TimeLine />, []);
  // const topBlockContent = useMemo(() => <Striped />, []);
  const bottomBlockContent = useMemo(
    () => (
      <>
        <BackGrid />
        <AddAppButton />
        <AppointmentsPlacer />
        <EventsPlacer />
        <RestrictedZones />
      </>
    ),
    []
  );

  const onTransitionEnd = useCallback(() => {
    console.log("trans end");
    setIsScrollingUI(false);
    setTranslateXPrevious(translateX);
  }, [setIsScrollingUI, translateX]);

  const topBlockRef = useRef<HTMLDivElement>(null);

  // sticky hook
  useLayoutEffect(() => {
    const element = topBlockRef.current as HTMLDivElement;
    StickyController.addStickyElement(element, 2, "timeline");
  }, []);

  const content = useMemo(
    () => (
      <>
        <div
          styleName="clipContainer"
          ref={topBlockRef}
          style={{ height: timeLineSize, width: width + 1 }}
        >
          <div
            styleName="topBlock scrollable"
            style={{
              transform,
              transitionDuration: `${scrollDuration}ms`,
              // clip: `rect(auto, ${viewIntervalPixelsWidth +
              //   2 -
              //   translateXPrevious}px, auto, auto)`
            }}
          >
            {topBlockContent}
          </div>
        </div>

        <div
          styleName="bottomBlock scrollable"
          style={{ transform, transitionDuration: `${scrollDuration}ms` }}
          onTransitionEnd={onTransitionEnd}
        >
          {bottomBlockContent}
        </div>
      </>
    ),
    [
      // translateXPrevious,
      width,
      timeLineSize,
      transform,
      scrollDuration,
      onTransitionEnd,
      topBlockContent,
      bottomBlockContent,
      // viewIntervalPixelsWidth
    ]
  );

  // const content = <div />;
  // console.log({ scrollJustReset });

  return useMemo(
    () => (
      <div
        styleName={classnames(
          "scrollingLayer",
          scrollJustReset ? "scrollIsJustReset" : ""
        )}
      >
        {content}
      </div>
    ),
    [content, scrollJustReset] // , self, scrollIsJustReset]
  );
};

export default ScrollingLayer;
