import './AppEditor.scss';

import React, { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';
import uuid from 'uuid';

import { AppointmentsInfo } from '../../../../stores/appointments';
import { generateUserInfo, User, UsersContainer } from '../../../../stores/users';
import { ActiveAppContainer } from '../../containers/ActiveAppContainer';
import { NewAppContainer } from '../../containers/NewAppContainer';
import AboBlock from './AboBlock';
import BodyInfoBlock from './BodyInfoBlock';
import IdBlock from './IdBlock';
import NfcCardBlock from './NfcCardBlock';

export enum UserField {
  Name,
  Id,
  Phone
}

export const SaveChangesContainer = createContainer(() => {
  const [saveCallback, setSaveCallback] = useState(() => () => {});
  return { saveCallback, setSaveCallback };
});

export const ValidityContainer = createContainer(() => {
  const [validityStore, setValidityStore] = useState<Record<string, boolean>>(
    {}
  );
  const setValidity = (key: string, val: boolean) =>
    validityStore[key] !== val
      ? setValidityStore({ ...validityStore, [key]: val })
      : undefined;

  const totalValidity = useMemo(
    () => Object.values(validityStore).every(v => v),
    [validityStore]
  );

  return { totalValidity, validityStore, setValidity };
});

const AppEditor = () => {
  const { activeAppId } = ActiveAppContainer.useContainer();
  const { getAppById, addAppointment } = AppointmentsInfo.useContainer();
  const { getUser, updateUser, addUser } = UsersContainer.useContainer();
  const { newApp, setNewApp } = NewAppContainer.useContainer();

  const [currentUser, setCurrentUser] = useState<User | undefined>(undefined);

  const [userNewName, setUserNewName] = useState("");
  const [userNewId, setUserNewId] = useState("");
  const [userNewPhone, setUserNewPhone] = useState("");

  const [userNewBodyInfo, setUserNewBodyInfo] = useState(
    generateUserInfo(true)
  );

  const { totalValidity } = ValidityContainer.useContainer();

  const self = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const callback = (e: MouseEvent) => {
      const selfElement = self.current;
      if (!selfElement) return;

      const { top, left, bottom, right } = selfElement.getBoundingClientRect();
      const { clientX: x, clientY: y } = e;

      if (x < left || x > right || y < top || y > bottom) {
        setCurrentUser(undefined);
        console.log("outside");
      }
    };
    window.addEventListener("click", callback);

    return () => window.removeEventListener("click", callback);
  }, []);

  useLayoutEffect(() => {
    setUserNewName(currentUser ? currentUser.name : "");
    setUserNewId(currentUser ? currentUser.id : "");
    setUserNewPhone(currentUser ? currentUser.phone : "");
    setUserNewBodyInfo(currentUser ? currentUser.info : generateUserInfo(true));

    // console.log(currentUser);
  }, [currentUser]);

  const createNewUser = async () => {
    if (!userNewName && !userNewPhone && !userNewId) return;

    const suchUserAlreadyExists = userNewId && !!(await getUser(userNewId));
    if (suchUserAlreadyExists) {
      console.warn("suchUserAlreadyExists", await getUser(userNewId));
      return;
    }

    const newUser: User = {
      id: userNewId || uuid.v4(),
      name: userNewName,
      phone: userNewPhone,
      info: userNewBodyInfo
    };

    setCurrentUser(newUser);
    updateUser(newUser);
  };

  const saveChanges = useCallback(async () => {
    if (!totalValidity || !currentUser) return;

    // if (newApp) {
    //   const user: User = (await getUser(userNewId)) || {
    //     id: uuid.v4(),
    //     name: userNewName,
    //     phone: userNewPhone,
    //     info: userNewBodyInfo
    //   };

    //   const daySchedule = await getDaySchedule(newApp.interval.start);

    //   const app: AppointmentUnassigned = {
    //     ...newApp,
    //     userId: user.id,
    //     user,
    //     interval: {
    //       start: newApp.interval.start,
    //       end: newApp.interval.start + daySchedule.gridStepDuration
    //     }
    //   };

    //   // console.log(app);

    //   addUser(user);
    //   addAppointment(app);
    //   setNewApp(undefined);
    //   // setActiveAppId(app.id);
    // } else
    updateUser({
      ...currentUser,
      name: userNewName,
      phone: userNewPhone,
      id: userNewId,
      info: userNewBodyInfo
    });
  }, [
    currentUser,
    userNewName,
    userNewPhone,
    userNewId,
    userNewBodyInfo,
    totalValidity,
    updateUser
  ]);

  const { setSaveCallback } = SaveChangesContainer.useContainer();
  useEffect(() => {
    setSaveCallback(() => saveChanges);
  }, [saveChanges, setSaveCallback]);

  const app = newApp ? newApp : activeAppId ? getAppById(activeAppId) : null;

  useEffect(() => {
    if (!app) return;

    (async () => {
      const user = await getUser(app.userId);
      setCurrentUser(user);
    })();
  }, [app, getUser]);

  const suggestAutofillRestrict = !!currentUser;
  const userLoaded = !!currentUser;
  // console.log({ userLoaded, currentUser });

  return (
    <div styleName="appEditor" ref={self}>
      <div styleName="wrapper">
        <IdBlock
          setCurrentUser={setCurrentUser}
          setUserNew={{
            id: setUserNewId,
            name: setUserNewName,
            phone: setUserNewPhone
          }}
          userNew={{ id: userNewId, phone: userNewPhone, name: userNewName }}
          suggestAutofillRestrict={suggestAutofillRestrict}
          userLoaded={userLoaded}
          createNewUser={createNewUser}
        />
        {currentUser ? (
          <>
            <hr />
            <AboBlock user={currentUser} />
            <hr />
            <NfcCardBlock user={currentUser} />
            <hr />
            <BodyInfoBlock
              setUserInfo={setUserNewBodyInfo}
              userInfo={userNewBodyInfo}
            />
          </>
        ) : null}
        {/* <hr /> */}
        {/* <Button onClick={saveChanges} disabled={!totalValidity} wrapper="div">
          Save Changes
        </Button> */}
      </div>
    </div>
  );
};

const Wrapper = () => (
  <ValidityContainer.Provider>
    <SaveChangesContainer.Provider>
      <AppEditor />
    </SaveChangesContainer.Provider>
  </ValidityContainer.Provider>
);

export default Wrapper;
