export const userIds = shell(rawNames.length).map(() => uuid.v4());

function generateAbo(): Abonnement {
  return {
    interval: { start: 0, end: 0 },
    visitsLeft: randomInt(0, 10),
    name: pickRandom(["Regular", "Single", "Gifted"])
  };
}

export function createUser(id: string): User {
  const randNameIndex = randomInt(0, rawNames.length - 1);
  const name = rawNames.splice(randNameIndex, 1)[0];
  const abo = Math.random() > 0.1 ? generateAbo() : undefined;
  const nfc = Math.random() > 0.3;
  const phone = (
    Math.floor(Math.random() * 9000000000) + 1000000000
  ).toString();
  const info = generateUserInfo(Math.random() > 0.8);

  return {
    id,
    name,
    phone,
    abo,
    nfc,
    info
  };
}

export const backendUsers = new Map<string, User>();

export async function fetchUser(
  id: string,
  onlyExisting = false
): Promise<User | null> {
  // await timeout(randomInt(100, 500));

  if (backendUsers.has(id)) {
    // console.log("cached");
    return backendUsers.get(id) as User;
  }

  if (onlyExisting) return null;

  // console.log(`fetch user ${id}`);

  return backendUsers.set(id, createUser(id)).get(id) as User;
}

export async function postUser(user: User): Promise<void> {
  console.log("post", user.id, user);
  backendUsers.set(user.id, user);
}
