import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsAppContainer = createContainer(() => {
  const [visibilityAvatar, setVisibilityAvatar] = useCookie(
    "visibilityAvatar",
    true
  );
  const [visibilityVisits, setVisibilityVisits] = useCookie(
    "visibilityVisits",
    true
  );

  const visibilityAvatarSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: visibilityAvatar,
    name: "Avatar",
    setChecked: setVisibilityAvatar,
  };

  const visibilityVisitsSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: visibilityVisits,
    name: "Visits",
    setChecked: setVisibilityVisits,
  };

  return {
    visibilityAvatarSetting,
    visibilityVisitsSetting,
    visibilityAvatar,
    visibilityVisits,
  };
});
