import classnames from 'classnames';
import { addDays, differenceInDays, startOfDay } from 'date-fns';
import React, { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { useDebounce, usePrevious, useWindowSize } from 'use-hooks';

import { OrientationContainer } from '../../containers/OrientationContainer';
import { SchedulesContainer } from '../../containers/SchedulesContainer';
import { ViewIntervalContainer } from '../../containers/ViewIntevalContainer';
import MonthChunk from './MonthChunk';
import styleVariables from './MonthRow.scss';

const monthChunkWidth = parseFloat(styleVariables.monthChunkWidth);
const monthChunkHeight = parseFloat(styleVariables.monthChunkHeight);
const monthChunksGap = parseFloat(styleVariables.monthChunksGap);
const monthChunksWidthTotal = monthChunkWidth + monthChunksGap;

const MonthRow = () => {
  // return <Striped />;
  const [selfData, setSelfData] = useState({
    offsetHeight: 0,
    offsetWidth: 0,
  });
  const { width } = useWindowSize();
  const widthD = useDebounce(width, 500);
  const {
    currentDay,
    currentDayPending,
    zeroAxisIsUpdating,
  } = ViewIntervalContainer.useContainer();
  const { zeroAxis, jumpToDay } = SchedulesContainer.useContainer();
  const { vertical } = OrientationContainer.useContainer();
  // const { isScrolling } = ScrollControlContainer.useContainer();

  // console.log(
  //   `${format(currentDay, "dd")} -> ${format(currentDayPending, "dd")}`
  // );

  const self = useRef<HTMLDivElement>(null);

  // self data collecting hook
  useLayoutEffect(() => {
    if (self.current) {
      const { offsetWidth, offsetHeight } = self.current;
      setSelfData({ offsetWidth, offsetHeight });
    }
  }, [widthD]);

  // const element = self.current;
  // const offsetTop = element ? element.offsetTop : -1;
  // // sticky hook
  // useEffect(() => {
  //   if (offsetTop === -1) return;
  //   // const element = self.current as HTMLDivElement;

  //   StickyController.addStickyElement(element as HTMLDivElement, 1);
  //   // }, []);
  // }, [element, offsetTop]);

  // const daysDelta = differenceInDays(currentDay, startOfDay(zeroAxis));

  const currentDayPrevious = usePrevious(currentDay);
  const daysDeltaSinceLast =
    differenceInDays(currentDay, currentDayPrevious) || 0;

  const buffer = Math.max(1, Math.abs(daysDeltaSinceLast));

  const chunksCountVisible = Math.floor(
    vertical
      ? selfData.offsetHeight / monthChunkHeight
      : selfData.offsetWidth / monthChunksWidthTotal
  );
  const chunksCountTotal = chunksCountVisible + buffer * 2;

  const daysDelta = differenceInDays(currentDay, startOfDay(zeroAxis));
  const daysDeltaAfterScrolling = differenceInDays(
    currentDayPending,
    startOfDay(zeroAxis)
  );

  const translateAnimated = -daysDelta * monthChunksWidthTotal;
  const translateInstant =
    (daysDeltaAfterScrolling - buffer) * monthChunksWidthTotal;

  const setCurrentDate = useCallback(
    (date: Date | number) => {
      jumpToDay(date);
    },
    [jumpToDay]
  );

  // PERFORMANCE_LEAK: monthrow chunks recalculation each scroll
  const chunks = useMemo(() => {
    if (zeroAxisIsUpdating) return null;

    const currentDayDate = new Date(currentDay);

    return new Array(chunksCountTotal).fill(null).map((_, i) => {
      const date = addDays(
        startOfDay(zeroAxis),
        i +
          daysDeltaAfterScrolling -
          buffer -
          Math.floor(chunksCountVisible / 2)
      );
      const current =
        currentDayDate.getDate() === date.getDate() &&
        currentDayDate.getMonth() === date.getMonth();
      return (
        <MonthChunk
          key={date.valueOf()}
          date={date.valueOf()}
          solid={current}
          appsCount={169}
          setCurrentDate={setCurrentDate}
        />
      );
    });
  }, [
    setCurrentDate,
    buffer,
    chunksCountTotal,
    chunksCountVisible,
    currentDay,
    daysDeltaAfterScrolling,
    zeroAxis,
    zeroAxisIsUpdating,
  ]);

  const chunksPrevious = usePrevious(chunks);
  const chunksFinal = chunks || chunksPrevious;

  return useMemo(() => {
    return (
      <div
        styleName={classnames("monthRow", vertical ? "vertical" : "horizontal")}
        ref={self}
        style={{
          transform: vertical
            ? `translateY(${translateInstant}px)`
            : `translateX(${translateInstant}px)`,
        }}
      >
        <div
          styleName="offsetController"
          style={{
            transform: vertical
              ? `translateY(${translateAnimated}px)`
              : `translateX(${translateAnimated}px)`,
            width: vertical
              ? "100%"
              : selfData.offsetWidth + buffer * 2 * monthChunksWidthTotal,
            height: vertical
              ? selfData.offsetHeight + buffer * 2 * monthChunksWidthTotal
              : "100%",
          }}
        >
          {chunksFinal}
        </div>
      </div>
    );
  }, [
    translateInstant,
    translateAnimated,
    selfData.offsetWidth,
    selfData.offsetHeight,
    chunksFinal,
    buffer,
    vertical,
  ]);
};

export default MonthRow;
