import './RestrictedZones.scss';

import { differenceInMilliseconds, eachDayOfInterval } from 'date-fns';
import { format, startOfDay } from 'date-fns/esm';
import React, { ReactNode, useCallback, useEffect, useMemo, useState } from 'react';

import { ColumnsContainer } from '../../../containers/ColumnsContainer';
import { DaysOffsetsContainer } from '../../../containers/DaysOffsetsContainer';
import { LevelBlocksContainer } from '../../../containers/LevelBlocksContainer';
import { RowsContainer } from '../../../containers/RowsContainer';
import { SchedulesContainer } from '../../../containers/SchedulesContainer';
import { ViewIntervalBufferedContainer } from '../../../containers/ViewIntervalBufferedContainer';
import RestrictedZone from './RestrictedZone/RestrictedZone';

const RestrictedZones = () => {
  const { levelBlocks } = LevelBlocksContainer.useContainer();
  const { viewIntervalBuffered } = ViewIntervalBufferedContainer.useContainer();
  const { zeroAxis, getDaySchedule } = SchedulesContainer.useContainer();
  const { columnWidth } = ColumnsContainer.useContainer();
  const { rowHeight, rowsCount } = RowsContainer.useContainer();
  const { dateToOffset } = DaysOffsetsContainer.useContainer();

  const [newZonesZeroAxis, setNewZonesZeroAxis] = useState(zeroAxis);
  const [newZones, setNewZones] = useState<Record<string, JSX.Element>>({});
  const [zones, setZones] = useState<Record<string, JSX.Element>>({});

  if (Object.keys(newZones).length > 0) {
    if (newZonesZeroAxis === zeroAxis) setZones({ ...zones, ...newZones });
    setNewZones({});
  }

  // console.log(Object.keys(zones).length);
  useMemo(() => {
    console.log("clear");
    setZones({});
    setNewZones({});
  }, [zeroAxis, columnWidth]);

  const generateZone = useCallback(
    async (interval: Interval) => {
      const dayStart = startOfDay(interval.start);
      const daySchedule = await getDaySchedule(dayStart);
      const duration = differenceInMilliseconds(
        interval.end.valueOf(),
        interval.start
      );

      const height = rowHeight * (rowsCount + 1 + levelBlocks.length);
      const width =
        columnWidth * (duration / daySchedule.gridStepDuration.valueOf());

      const x = await dateToOffset(interval.start);
      const y = 0;

      return (
        <RestrictedZone
          {...{ x, y, width, height, mark: format(dayStart, "dd") }}
          key={interval.start.valueOf()}
        />
      );
    },
    [
      columnWidth,
      rowsCount,
      rowHeight,
      levelBlocks.length,
      getDaySchedule,
      dateToOffset
    ]
  );

  // useWhyDidYouUpdate("restrictedZones", { viewInterval });
  useEffect(() => {
    // console.log(`generate for ${intervalToString(viewInterval)}`);

    eachDayOfInterval(viewIntervalBuffered).forEach(async (day: Date) => {
      const daySchedule = await getDaySchedule(day);
      const restrictedIntervals = daySchedule.restrictedZones;

      if (!restrictedIntervals.length) return;

      const zones = (
        await Promise.all(
          restrictedIntervals.map(
            async interval => await generateZone(interval)
          )
        )
      ).reduce((acc, val) => ({ ...acc, [val.key as string]: val }), {});

      setNewZonesZeroAxis(zeroAxis);
      setNewZones(zones);
    }, []);
  }, [viewIntervalBuffered]);

  return useMemo(() => {
    // console.log(zones.length);
    return <div styleName="restrictedZones">{Object.values(zones)}</div>;
  }, [zones]);
};

export default RestrictedZones;
