import './BottomBlock.scss';

import classnames from 'classnames';
import React, { useEffect, useMemo, useRef } from 'react';

import { AddAppButtonContainer } from '../containers/AddAppButtonContainer';
import { DraggingAutoScrollContainer } from '../containers/DraggingAutoScrollContainer';
import { OrientationContainer } from '../containers/OrientationContainer';
import { SettingsLevelsBlockContainer } from '../containers/SettingsLevelsBlockContainer';
import { TimeLineContainer } from '../containers/TimeLineContainer';
import ControlBlock from '../ControlBlock';
import LevelsBlock from '../LevelsBlock';
import MainBlock from '../MainBlock';
import TimeLine from '../MainBlock/ScrollingLayer/TimeLine';
import MonthRow from '../TopBlock/MonthRow';

const BottomBlock = () => {
  const { vertical } = OrientationContainer.useContainer();
  const { visibleLevelsBlock } = SettingsLevelsBlockContainer.useContainer();

  return useMemo(
    () => (
      <div
        styleName={classnames(
          "bottomBlock",
          vertical ? "vertical" : "horizontal",
          visibleLevelsBlock ? "" : "noLevelsBlock"
        )}
      >
        {vertical ? (
          <>
            <MonthRow />
            <TimeLineContainer.Provider>
              <TimeLine />
              <DraggingAutoScrollContainer.Provider>
                <MainBlock />
              </DraggingAutoScrollContainer.Provider>
              <ControlBlock />
            </TimeLineContainer.Provider>
          </>
        ) : (
          <>
            {visibleLevelsBlock ? <LevelsBlock /> : null}
            <TimeLineContainer.Provider>
              <DraggingAutoScrollContainer.Provider>
                <AddAppButtonContainer.Provider>
                  <MainBlock />
                </AddAppButtonContainer.Provider>
              </DraggingAutoScrollContainer.Provider>
              <ControlBlock />
            </TimeLineContainer.Provider>
          </>
        )}
      </div>
    ),
    [vertical, visibleLevelsBlock]
  );
};

export default BottomBlock;
