import './IdBlock.scss';

import React, { useCallback, useLayoutEffect, useMemo, useState } from 'react';

import { User, UsersContainer } from '../../../../../stores/users';
import { SaveChangesContainer, UserField } from '../AppEditor';
import IdInput from './IdInput';

export interface IdBlockProps {
  userNew: {
    name: string;
    phone: string;
    id: string;
  };
  setUserNew: {
    name: (val: string) => void;
    phone: (val: string) => void;
    id: (val: string) => void;
  };
  suggestAutofillRestrict: boolean;
  setCurrentUser: (user: User) => void;
  userLoaded: boolean;
  createNewUser: () => void;
}

const IdBlock = ({
  userLoaded,
  userNew,
  setUserNew,
  setCurrentUser,
  suggestAutofillRestrict,
  createNewUser
}: IdBlockProps) => {
  const { getUsersStartingWith } = UsersContainer.useContainer();
  const { saveCallback } = SaveChangesContainer.useContainer();

  const onInputChangeUserName = useCallback(
    (value: string) => {
      setUserNew.name(value);
    },
    [setUserNew]
  );

  const onInputChangeUserPhone = useCallback(
    (value: string) => {
      setUserNew.phone(value);
    },
    [setUserNew]
  );

  const onInputChangeUserId = useCallback(
    (value: string) => {
      setUserNew.id(value);
    },
    [setUserNew]
  );

  const [usersStartingWithName, setUsersStartingWithName] = useState<User[]>(
    []
  );
  const [usersStartingWithPhone, setUsersStartingWithPhone] = useState<User[]>(
    []
  );
  const [usersStartingWithId, setUsersStartingWithId] = useState<User[]>([]);

  useMemo(
    async () =>
      setUsersStartingWithName(
        await getUsersStartingWith(userNew.name, UserField.Name)
      ),
    [getUsersStartingWith, userNew.name]
  );

  useMemo(
    async () =>
      setUsersStartingWithPhone(
        await getUsersStartingWith(userNew.phone, UserField.Phone)
      ),
    [getUsersStartingWith, userNew.phone]
  );

  useMemo(
    async () =>
      setUsersStartingWithId(
        await getUsersStartingWith(userNew.id, UserField.Id)
      ),
    [getUsersStartingWith, userNew.id]
  );

  return (
    <div styleName="idBlock">
      <IdInput
        onChange={onInputChangeUserName}
        onBlur={saveCallback}
        value={userNew.name}
        suggestAutofill={!suggestAutofillRestrict && userNew.name.length > 1}
        suggestingUsers={usersStartingWithName}
        placeholder={"client name"}
        setCurrentUser={setCurrentUser}
        forcedValid={!userLoaded}
        createNewUser={createNewUser}
      />
      <IdInput
        onBlur={saveCallback}
        onChange={onInputChangeUserPhone}
        value={userNew.phone}
        suggestAutofill={!suggestAutofillRestrict && userNew.phone.length > 1}
        suggestingUsers={usersStartingWithPhone}
        placeholder={"client phone"}
        setCurrentUser={setCurrentUser}
        forcedValid={!userLoaded}
        createNewUser={createNewUser}
      />
      <IdInput
        onBlur={saveCallback}
        onChange={onInputChangeUserId}
        value={userNew.id}
        suggestAutofill={!suggestAutofillRestrict && userNew.id.length > 1}
        suggestingUsers={usersStartingWithId}
        placeholder={"client id"}
        setCurrentUser={setCurrentUser}
        forcedValid={!userLoaded}
        createNewUser={createNewUser}
      />
    </div>
  );
};

export default IdBlock;
