import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsAddAppButtonContainer = createContainer(() => {
  const [addAppButtonOn, setAddAppButtonOn] = useCookie(
    "addAppButtonOn",
    false
  );

  const addAppButtonOnSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: addAppButtonOn,
    name: "Display AddAppButton",
    setChecked: setAddAppButtonOn,
  };

  return { addAppButtonOn, addAppButtonOnSetting };
});
