import { useEffect, useLayoutEffect, useState } from 'react';
import { createContainer } from 'unstated-next';

import { WrapperContainer } from './WrapperContainer';

export const RowsContainer = createContainer(
  (
    { rowHeightInitial }: { rowHeightInitial: number } = {
      rowHeightInitial: 50,
    }
  ) => {
    const { setHeight } = WrapperContainer.useContainer();
    const [rowsCount, setRowsCount] = useState(15);
    const [rowHeight, setRowHeight] = useState(rowHeightInitial);

    // const [rowHeight, setRowHeight] = useState(rowHeightInitial);

    useLayoutEffect(() => {
      setHeight(rowsCount * rowHeight);
    }, [rowsCount, rowHeight, setHeight]);

    return { rowHeight, rowsCount, setRowHeight, setRowsCount };
  }
);
