import './AppointmentCell.scss';

import React, { useEffect, useState } from 'react';

import { AppointmentNoUser } from '../../../../../../../stores/appointments';
import GridCell from '../../../GridCell';

export interface AppointmentCellProps {
  app: AppointmentNoUser;
}

const colors = [
  "rgba(168, 66, 50, 0.3)",
  "rgba(168, 146, 50, 0.3)",
  "rgba(103, 168, 50, 0.3)",
  "rgba(50, 168, 99, 0.3)",
  "rgba(50, 158, 168, 0.3)",
  "rgba(50, 99, 168, 0.3)",
  "rgba(121, 50, 168, 0.3)",
  "rgba(154, 50, 168, 0.3)",
  "rgba(50, 164, 168, 0.3)",
  "rgba(76, 168, 50, 0.3)",
  "rgba(168, 50, 50, 0.3)",
  "rgba(168, 50, 76, 0.3)"
];
const blocks = new Set();

const AppointmentCell = (props: AppointmentCellProps) => {
  const { app } = props;
  const { user } = app;

  if (!blocks.has(app.blockId)) blocks.add(app.blockId);

  const color = colors[[...blocks].indexOf(app.blockId) % colors.length];
  const counter = 15;
  const info = user ? user.phone : "";
  const name = user ? user.name : "not loaded";

  return <GridCell {...{ color, counter, info, name }} />;
};

export default AppointmentCell;
