import './BodyInfoBlock.scss';

import React, { useCallback, useLayoutEffect, useState } from 'react';

import { UserInfo } from '../../../../../stores/users';
import { SaveChangesContainer, ValidityContainer } from '../AppEditor';
import LabeledInput from '../LabeledInput';

export interface BodyInfoBlockProps {
  userInfo: UserInfo;
  setUserInfo: (val: UserInfo) => void;
}

const BodyInfoBlock = ({ userInfo, setUserInfo }: BodyInfoBlockProps) => {
  const { sizes, weight } = userInfo;
  const { setValidity } = ValidityContainer.useContainer();
  const { saveCallback } = SaveChangesContainer.useContainer();

  return (
    <div styleName="bodyInfoBlock">
      {Object.entries(sizes).map(([key, value]) => (
        <LabeledInput
          onBlur={saveCallback}
          type="number"
          key={key}
          label={`${key} size:`}
          value={value}
          onChange={(val: string) =>
            setUserInfo({
              ...userInfo,
              sizes: { ...userInfo.sizes, [key]: val }
            })
          }
          setValidity={setValidity}
        />
      ))}
      <LabeledInput
        onBlur={saveCallback}
        type="number"
        label="Weight:"
        value={weight}
        onChange={(val: string) => setUserInfo({ ...userInfo, weight: val })}
        setValidity={setValidity}
      />
    </div>
  );
};

export default BodyInfoBlock;
