import './InputNumber.scss';

import classnames from 'classnames';
import React, { InputHTMLAttributes } from 'react';

import InputWrapper from '../InputWrapper';

const InputNumber = (
  attrs: InputHTMLAttributes<any> & {
    wrapper: "span" | "div";
    invalid?: boolean;
  }
) => {
  const { wrapper, invalid, ...inputProps } = attrs;
  // console.log(attrs);

  return (
    <InputWrapper wrapper={wrapper}>
      <input
        type="number"
        styleName={classnames("inputNumber", invalid ? "invalid" : "")}
        {...inputProps}
      />
    </InputWrapper>
  );
};

export default InputNumber;
