import './SettingBlock.scss';

import React, { useEffect, useState } from 'react';

import { SettingChunk, SettingType } from '../../containers/SettingsContainer';

const SettingBlock = ({ setting }: { setting: SettingChunk }) => {
  const [settingTemp, setSettingTemp] = useState(setting);

  useEffect(() => {
    setSettingTemp(setting);
  }, [setting]);

  const generateIntervalBody = () => {
    switch (settingTemp.type) {
      case SettingType.Checkbox: {
        const { checked, setChecked, disabled } = settingTemp;
        return (
          <input
            type="checkbox"
            checked={checked}
            onChange={e => setChecked(e.target.checked)}
            disabled={disabled}
          />
        );
      }
      case SettingType.Range: {
        const { min, max, value, setNewValue } = settingTemp;
        return (
          <input
            type="range"
            min={min}
            max={max}
            value={value}
            onChange={e =>
              setSettingTemp({
                ...settingTemp,
                value: parseInt((e.target as HTMLInputElement).value)
              })
            }
            onMouseUp={(e: React.MouseEvent<HTMLInputElement>) =>
              setNewValue(parseInt((e.target as HTMLInputElement).value))
            }
          />
        );
      }
    }
  };

  return (
    <div>
      {`${setting.name}: `}
      {generateIntervalBody()}
    </div>
  );
};

export default SettingBlock;
