import { addDays, addMilliseconds, differenceInDays, differenceInMilliseconds, subDays } from 'date-fns';
import { startOfDay } from 'date-fns/esm';
import { List } from 'immutable';
import { useCallback, useLayoutEffect, useMemo, useRef } from 'react';
import { createContainer } from 'unstated-next';
import { usePrevious } from 'use-hooks';

import { ColumnsContainer } from './ColumnsContainer';
import { DaySchedule, Schedule, SchedulesContainer } from './SchedulesContainer';

interface DaysInfo {
  daysBeforeZeroAxis: number;
  daysOffsets: List<number>;
}

const daysInfoCache = new window.Map<Schedule, DaysInfo>();

function dateToOffsetInstant(
  date: Date | number,
  daySchedule: DaySchedule,
  columnWidth: number,
  dayOffsetPixels: number
) {
  const dayDuration = daySchedule.dayDuration;
  const columnsCount = dayDuration / daySchedule.gridStepDuration;
  const dayWidth = columnWidth * columnsCount;

  const remainDuration = differenceInMilliseconds(
    date,
    daySchedule.dayInterval.start
  );
  const percentage = remainDuration / dayDuration;

  const inDayOffset = dayWidth * percentage;

  const offset = dayOffsetPixels + inDayOffset;

  return offset;
}

export const DaysOffsetsContainer = createContainer(() => {
  const {
    zeroAxis,
    schedule,
    scheduleWithDay
  } = SchedulesContainer.useContainer();
  const { columnWidth } = ColumnsContainer.useContainer();
  const columnWidthPrevious = usePrevious(columnWidth);
  useMemo(() => {
    if (columnWidth !== columnWidthPrevious) daysInfoCache.clear();
  }, [columnWidthPrevious, columnWidth]);

  const calcDaysInfo = useCallback(
    (schedule: Schedule): DaysInfo => {
      const days = schedule
        .toSeq()
        .sortBy((_, key) => key)
        .toList();

      if (!days.count())
        return { daysOffsets: List<number>(), daysBeforeZeroAxis: 0 };

      const offsets = days.reduce(
        (
          { offset, daysOffset }: { offset: number; daysOffset: number[] },
          day
        ) => {
          const columnsCount =
            differenceInMilliseconds(
              day.dayInterval.end,
              day.dayInterval.start
            ) / day.gridStepDuration;
          const dayWidth = columnsCount * columnWidth;

          return {
            offset: offset + dayWidth,
            daysOffset: [...daysOffset, offset]
          };
        },
        { offset: 0, daysOffset: [] }
      );

      const daysBeforeZeroAxis = differenceInDays(
        startOfDay(zeroAxis),
        startOfDay((days.get(0) as DaySchedule).dayInterval.start)
      );
      const daysBeforeZeroAxisOffset = offsets.daysOffset[daysBeforeZeroAxis];

      const daysOffsetsRelative = offsets.daysOffset.map(
        offset => offset - daysBeforeZeroAxisOffset
      );

      if (isNaN(daysOffsetsRelative[0])) debugger;

      return {
        daysOffsets: List(daysOffsetsRelative),
        daysBeforeZeroAxis
      };
    },
    [columnWidth, zeroAxis]
  );

  const getDaysInfo = useCallback(
    (schedule: Schedule): DaysInfo => {
      if (daysInfoCache.has(schedule))
        return daysInfoCache.get(schedule) as DaysInfo;

      const daysInfo = calcDaysInfo(schedule);
      daysInfoCache.set(schedule, daysInfo);
      return daysInfo;
    },
    [calcDaysInfo]
  );

  const offsetToDate = async (
    offset: number,
    scheduleCurrent = schedule
  ): Promise<number> => {
    const { daysBeforeZeroAxis, daysOffsets } = getDaysInfo(scheduleCurrent);
    // console.log(
    //   scheduleCurrent
    //     .toArray()
    //     .map(([key, value]) => [format(key, "dd HH:mm"), value])
    //     .map(([key]) => key),
    //   daysOffsets.toArray(),
    //   daysBeforeZeroAxis,
    //   offset
    // );

    const dayIndex = daysOffsets.findLastIndex(
      dayOffset => offset - dayOffset >= -1
    );

    // console.log(
    //   dayIndex,
    //   scheduleCurrent
    //   .toArray()
    //   .map(([key, value]) => [format(key, "dd HH:mm"), value]).map(([key]) => key),
    //   daysOffsets.toArray(),
    //   offset
    // );

    if (dayIndex === -1) {
      const newDay = subDays(zeroAxis, daysBeforeZeroAxis + 1);
      const scheduleNew = await scheduleWithDay(newDay, scheduleCurrent);

      return offsetToDate(offset, scheduleNew);
    }

    if (dayIndex === daysOffsets.count() - 1) {
      const newDay = addDays(
        zeroAxis,
        daysOffsets.count() - daysBeforeZeroAxis
      );
      // console.log(
      //   `fetch ${format(newDay, "dd")} (${daysOffsets.count() -
      //     daysBeforeZeroAxis +
      //     1} = ${daysOffsets.count()} - ${daysBeforeZeroAxis} + 1) `
      // );
      // console.log("was:", scheduleCurrent.size);
      const scheduleNew = await scheduleWithDay(newDay, scheduleCurrent);
      // console.log("now:", scheduleNew.size);

      return offsetToDate(offset, scheduleNew);
    }

    const day = addDays(zeroAxis, dayIndex - daysBeforeZeroAxis);
    const dayId = startOfDay(day).valueOf();
    const {
      columnsCount,
      gridStepDuration,
      dayInterval: dayInteval
    } = scheduleCurrent.get(dayId) as DaySchedule;

    const dayWidth = columnsCount * columnWidth || 1;

    const offsetDay = daysOffsets.get(dayIndex) as number;
    const offsetRemain = offset - offsetDay;
    const relativeX = offsetRemain / dayWidth;

    const stepsPast = Math.floor(columnsCount * relativeX);
    const timePast = gridStepDuration * stepsPast;

    const date = addMilliseconds(dayInteval.start, timePast);

    return date.valueOf();
  };

  const dateToOffsetSafe = async (
    date: Date | number,
    scheduleCurrent = schedule
  ): Promise<number> => {
    const { daysOffsets, daysBeforeZeroAxis } = getDaysInfo(scheduleCurrent);
    const dateDayStart = startOfDay(date);
    const daysOffset =
      differenceInDays(dateDayStart, startOfDay(zeroAxis)) + daysBeforeZeroAxis;

    const daySchedule = scheduleCurrent.get(
      startOfDay(date).valueOf()
    ) as DaySchedule;

    // if (!daySchedule) {
    //   console.warn(
    //     `no daySchedule for ${format(date, "dd")}`,
    //     scheduleCurrent.toArray()
    //   );
    //   return 0;
    // }
    if (!daySchedule) {
      const newSchedule = await scheduleWithDay(date, scheduleCurrent);
      return dateToOffsetSafe(date, newSchedule);
    }

    const daysOffsetPixels = daysOffsets.get(daysOffset) as number;
    const dayDuration = daySchedule.dayDuration;
    const columnsCount = dayDuration / daySchedule.gridStepDuration;
    const dayWidth = columnWidth * columnsCount;

    const remainDuration = differenceInMilliseconds(
      date,
      daySchedule.dayInterval.start
    );
    const percentage = remainDuration / dayDuration;

    const inDayOffset = dayWidth * percentage;

    const offset = daysOffsetPixels + inDayOffset;
    if (isNaN(offset)) {
      console.warn("offset is NaN! (returning 0 to prevent infinite fetching");
      return 0;
    }

    return offset;
  };

  const dateToOffsetMapActual = useRef<
    Map<number, (dateRaw: Date | number) => number>
  >(new Map());

  const dateToOffsetZero = useCallback(() => 0, []);

  const generateDateToOffsetLocal = useCallback(
    (dateDay: number) => {
      const daySchedule = schedule.get(dateDay);

      if (!daySchedule) return dateToOffsetZero;

      const { daysOffsets, daysBeforeZeroAxis } = getDaysInfo(schedule);
      const daysOffset =
        differenceInDays(dateDay, startOfDay(zeroAxis)) + daysBeforeZeroAxis;
      const dayOffsetPixels = daysOffsets.get(daysOffset) as number;

      return (dateRaw: Date | number) =>
        dateToOffsetInstant(dateRaw, daySchedule, columnWidth, dayOffsetPixels);
    },
    [schedule, columnWidth, dateToOffsetZero, getDaysInfo, zeroAxis]
  );

  const getDateToOffsetLocal = useCallback(
    (dateRaw: Date | number) => {
      const dateToOffsetMap = dateToOffsetMapActual.current;
      const dateDay = startOfDay(dateRaw).valueOf();

      if (dateToOffsetMap.has(dateDay)) {
        const func = dateToOffsetMap.get(dateDay);
        if (func === dateToOffsetZero) {
          const funcNew = generateDateToOffsetLocal(dateDay);
          if (funcNew !== func) dateToOffsetMap.set(dateDay, funcNew);
        }
      }

      return dateToOffsetMap.get(dateDay);
    },
    [generateDateToOffsetLocal, dateToOffsetZero]
  );

  useLayoutEffect(() => {
    dateToOffsetMapActual.current.clear();
  }, [columnWidth, zeroAxis]);

  const dateToOffset = (
    date: Date | number,
    scheduleCurrent = schedule
  ): number => {
    const { daysOffsets, daysBeforeZeroAxis } = getDaysInfo(scheduleCurrent);
    const dateDayStart = startOfDay(date);
    const daysOffset =
      differenceInDays(dateDayStart, startOfDay(zeroAxis)) + daysBeforeZeroAxis;

    const daySchedule = scheduleCurrent.get(
      startOfDay(date).valueOf()
    ) as DaySchedule;

    if (!daySchedule) {
      // console.warn(
      //   `no daySchedule for ${format(date, "dd")}`,
      //   scheduleCurrent.toArray()
      // );
      return 0;
    }
    // if (!daySchedule) {
    //   const newSchedule = await scheduleWithDay(date, scheduleCurrent);
    //   return dateToOffset(date, newSchedule);
    // }

    const dayOffsetPixels = daysOffsets.get(daysOffset) as number;
    const offset = dateToOffsetInstant(
      date,
      daySchedule,
      columnWidth,
      dayOffsetPixels
    );
    if (isNaN(offset)) debugger;

    return offset;
  };

  return { offsetToDate, dateToOffsetSafe, dateToOffset, getDateToOffsetLocal };
});
