import './MainBlock.scss';

import { List } from 'immutable';
import React, { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { useDebounce, usePrevious, useWindowSize } from 'use-hooks';

import { addAnimation, removeAnimation } from '../../../assembly/animationController';
import { areIntervalsIntersects, offsetInterval, withinInterval } from '../../../assembly/interval';
import { Appointment, AppointmentsBlock, AppointmentsInfo, Result } from '../../../stores/appointments';
import { ActiveAppContainer } from '../containers/ActiveAppContainer';
import { AddAppButtonContainer } from '../containers/AddAppButtonContainer';
import { ColumnsContainer } from '../containers/ColumnsContainer';
import { DaysOffsetsContainer } from '../containers/DaysOffsetsContainer';
import { DraggingAutoScrollContainer } from '../containers/DraggingAutoScrollContainer';
import { DraggingContainer } from '../containers/DraggingContainer';
import { InitializeStateContainer } from '../containers/InitializeStateContainer';
import { LevelBlocksContainer } from '../containers/LevelBlocksContainer';
import { NewAppContainer } from '../containers/NewAppContainer';
import { RowsContainer } from '../containers/RowsContainer';
import { SchedulesContainer } from '../containers/SchedulesContainer';
import { ScrollControlContainer } from '../containers/ScrollControlContainer';
import { SettingsAddAppButtonContainer } from '../containers/SettingsAddAppButtonContainer';
import { SettingsLevelsBlockContainer } from '../containers/SettingsLevelsBlockContainer';
import { TimeLineContainer } from '../containers/TimeLineContainer';
import { ViewIntervalPixelsContainer } from '../containers/ViewIntervalPixelsContainer';
import { ViewIntervalContainer } from '../containers/ViewIntevalContainer';
import { WrapperContainer } from '../containers/WrapperContainer';
import ScrollingLayer from './ScrollingLayer';
import StaticLayer from './StaticLayer';

export interface Props {
  // scrollOffset: number;
  minColumnWidth: number;
}

interface SelfData {
  offsetWidth: number;
  offsetHeight: number;
  offsetLeft: number;
  offsetTop: number;
}

const MainBlock = () => {
  // const { columnWidth, setContainerWidth } = ColumnsInfo.useContainer();
  // const { rowHeight, rowsCount } = RowsInfo.useContainer();
  // const { levelBlocks, indexesMap } = levelBlocksInfo.useContainer();
  // const {
  //   scrollOffset,
  //   oneScrollSizePixels,
  //   isScrolling,
  //   scrollLeft,
  //   scrollRight
  // } = ScrollInfo.useContainer();
  const {
    draggingApp,
    setDraggingApp,
    dragOffsetToAppCell,
    isDragging,
    onDragging,
    onDraggingApart,
  } = DraggingContainer.useContainer();
  // const { timeLineHeight } = TimeLineInfo.useContainer();
  const {
    getApp,
    tryToFreePlace,
    setAppointmentBlocks,
  } = AppointmentsInfo.useContainer();
  const { getDaySchedule } = SchedulesContainer.useContainer();
  // const {
  //   thresholdPixelsHorizontal,
  //   thresholdPixelsVertical
  // } = DraggingAutoScrollInfo.useContainer();

  // const {
  //   stateOnDragging: {
  //     state: [onDragging]
  //   },
  //   stateOnDraggingApart: {
  //     state: [onDraggingApart]
  //   }
  // } = DragSettingsInfo.useContainer();
  // const { offsetToDate } = DaysOffsetsInfo.useContainer();

  const {
    setDate,
    setLevel,
    setVisible,
    visible: addAppButtonVisible,
  } = AddAppButtonContainer.useContainer();
  // const { orientation } = OrientationContainer.useContainer();
  const { newApp, setNewApp } = NewAppContainer.useContainer();
  const {
    setHeight,
    setWidth: setWrapperWidth,
  } = WrapperContainer.useContainer();
  const { initialized } = InitializeStateContainer.useContainer();
  const { isScrollingInitialized } = ViewIntervalContainer.useContainer();
  const {
    // isScrollingInProgress,
    viewIntervalPixels,
    viewIntervalPixelsWidth,
  } = ViewIntervalPixelsContainer.useContainer();
  const {
    thresholdPixelsHorizontal,
    thresholdPixelsVertical,
  } = DraggingAutoScrollContainer.useContainer();
  const { scrollRight, scrollLeft } = ScrollControlContainer.useContainer();
  const { timeLineSize } = TimeLineContainer.useContainer();
  const { rowHeight, rowsCount } = RowsContainer.useContainer();
  const { columnWidth, columnsCount } = ColumnsContainer.useContainer();
  const { indexesMap, levelBlocks } = LevelBlocksContainer.useContainer();
  const { offsetToDate } = DaysOffsetsContainer.useContainer();
  const { setActiveAppId } = ActiveAppContainer.useContainer();

  const { width: windowWidth } = useWindowSize();
  const windowWidthD = useDebounce(windowWidth, 500);

  const { visibleLevelsBlock } = SettingsLevelsBlockContainer.useContainer();
  const { addAppButtonOn } = SettingsAddAppButtonContainer.useContainer();

  const [selfData, setSelfData] = useState<SelfData>({
    offsetHeight: 0,
    offsetWidth: 0,
    offsetLeft: 0,
    offsetTop: 0,
  });
  const self = useRef<HTMLDivElement>(null);
  const [dragStartScrollY, setDragStartScrollY] = useState(0);

  const loaded = !!selfData.offsetWidth;

  // self data collecting hook
  useLayoutEffect(() => {
    if (self.current) {
      const { offsetWidth, offsetHeight, offsetTop, offsetLeft } = self.current;
      setSelfData({ offsetWidth, offsetHeight, offsetTop, offsetLeft });
    }
  }, [windowWidthD, self, visibleLevelsBlock]);

  // useLayoutEffect(() => {
  //   if (loaded) setWrapperWidth(selfData.offsetWidth);
  // }, [selfData.offsetWidth, loaded, setWrapperWidth]);

  const [relativeTriggerLeft, setRelativeTriggerLeft] = useState(Infinity);
  const [relativeTriggerRight, setRelativeTriggerRight] = useState(Infinity);
  const [relativeTriggerTop, setRelativeTriggerTop] = useState(Infinity);
  const [relativeTriggerBottom, setRelativeTriggerBottom] = useState(Infinity);
  const [anyAnimationActive, setAnyAnimationActive] = useState(false);

  const scrollAnimationTop = useCallback(
    (delta: number) => window.scrollBy(0, -1 * delta),
    []
  );
  const scrollAnimationBottom = useCallback(
    (delta: number) => window.scrollBy(0, 1 * delta),
    []
  );

  const clearAnimations = useCallback(() => {
    removeAnimation(scrollAnimationTop);
    removeAnimation(scrollAnimationBottom);

    setAnyAnimationActive(false);
  }, [scrollAnimationTop, scrollAnimationBottom]);

  const tryClearAnimations = useCallback(() => {
    if (anyAnimationActive) clearAnimations();
  }, [anyAnimationActive, clearAnimations]);

  if (!isScrollingInitialized && isDragging) {
    if (relativeTriggerLeft < thresholdPixelsHorizontal) scrollLeft();
    if (relativeTriggerRight < thresholdPixelsHorizontal) scrollRight();

    if (relativeTriggerTop < thresholdPixelsVertical) {
      if (!anyAnimationActive) {
        addAnimation(scrollAnimationTop);
        setAnyAnimationActive(true);
      }
    } else if (relativeTriggerBottom < thresholdPixelsVertical) {
      if (!anyAnimationActive) {
        addAnimation(scrollAnimationBottom);
        setAnyAnimationActive(true);
      }
    } else tryClearAnimations();
  } else tryClearAnimations();

  const getRowColumn = useCallback(
    (pageX: number, pageY: number, processor = (a: number) => a) => {
      const realX = pageX - selfData.offsetLeft;
      const realY = pageY - selfData.offsetTop;

      const absoluteX = realX + viewIntervalPixels.start;
      const absoluteY = realY - timeLineSize;

      const row = processor(absoluteY / rowHeight);
      const column = processor(absoluteX / columnWidth);

      const rowNormalized =
        indexesMap[row + 1] === indexesMap[row] ? -1 : indexesMap[row];

      const rowNormalizedNormalized = Math.min(
        Math.max(rowNormalized, 0),
        rowsCount
      );
      const columnNormalized = column;

      return {
        row: rowNormalizedNormalized,
        column: columnNormalized,
        outOfBoundary: rowNormalizedNormalized !== rowNormalized,
      };
    },
    [
      selfData,
      rowHeight,
      columnWidth,
      timeLineSize,
      rowsCount,
      indexesMap,
      viewIntervalPixels.start,
    ]
  );

  const getDate = useCallback(
    async (column: number) => {
      const offsetPixels = column * columnWidth;
      // console.log("request by MainBlock");
      return (await offsetToDate(offsetPixels)).valueOf();
    },
    [offsetToDate, columnWidth]
  );

  const mouseDownHandler = useCallback(
    async (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      if (!onDragging) return;

      const { pageX, pageY } = event;
      const { column, row, outOfBoundary } = getRowColumn(
        pageX,
        pageY,
        Math.floor
      );

      if (outOfBoundary) return;

      const date = await getDate(column);

      const daySchedule = await getDaySchedule(date);

      // check if column is restricted
      const zones = daySchedule.restrictedZones;

      const restricted = zones.some((zone) =>
        withinInterval(date, zone, true, false)
      );

      if (restricted) return;

      const app = getApp(date, row);
      console.log({ row, column, app, outOfBoundary });

      if (app) {
        setNewApp(undefined);

        setActiveAppId(app.id);
        setDraggingApp(app);
        setDragStartScrollY(window.scrollY);

        if (addAppButtonVisible) setVisible(false);
      }
      // DISABLED_FUNCTIONAL: new app button at touch place
      // else
      //   setNewApp({
      //     id: uuid.v4(),
      //     interval: { start: date, end: date },
      //     rowIndex: row,
      //     userId: uuid.v4()
      //   });
    },
    [
      getRowColumn,
      getApp,
      setDraggingApp,
      getDate,
      onDragging,
      setActiveAppId,
      addAppButtonVisible,
      setVisible,
      setNewApp,
      getDaySchedule,
    ]
  );

  const mouseUpHandler = useCallback(
    async (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      // console.log("mouse up", draggingApp);
      if (!draggingApp) return;
      //  console.log("mouse up (with draggingApp)");

      const { pageX, pageY } = event;
      const { dx, dy } = dragOffsetToAppCell;
      const deltaScrollY = window.scrollY - dragStartScrollY;
      const anchorX = pageX - dx;
      const anchorY = pageY - dy + window.scrollY - deltaScrollY; //  + rowHeight / 2;

      const { row, column, outOfBoundary } = getRowColumn(
        anchorX,
        anchorY,
        Math.round
      );

      const currentDateStart = await getDate(column);
      const dateOffset = currentDateStart - draggingApp.interval.start;
      const newInterval = offsetInterval(draggingApp.interval, dateOffset);

      const daySchedule = await getDaySchedule(newInterval.start);

      // check if column is restricted
      const zones = daySchedule.restrictedZones;

      const restricted = zones.some((zone) =>
        areIntervalsIntersects(zone, newInterval, false, false)
      );
      if (restricted || outOfBoundary) {
        setDraggingApp(null);
        return;
      }

      const overlapsNextDay =
        newInterval.end > daySchedule.dayInterval.end.valueOf();
      // console.log(
      //   [newInterval.end, (newInterval.start)].map(date =>
      //     format(date, "dd HH:mm")
      //   )
      // );

      if (overlapsNextDay) {
        setDraggingApp(null);
        return;
      }

      const updatedApp: Appointment = {
        ...draggingApp,
        rowIndex: row,
        interval: newInterval,
      };

      const [shiftResult, newAppBlocks, nothingChanged] = tryToFreePlace(
        updatedApp.id,
        updatedApp.rowIndex,
        updatedApp.interval
      );

      if (shiftResult === Result.Success && (nothingChanged || onDraggingApart))
        setAppointmentBlocks(newAppBlocks as List<AppointmentsBlock>);

      setDraggingApp(null);
      setRelativeTriggerLeft(Infinity);
      setRelativeTriggerRight(Infinity);
      setRelativeTriggerTop(Infinity);
      setRelativeTriggerBottom(Infinity);
    },
    [
      draggingApp,
      getRowColumn,
      getDate,
      setDraggingApp,
      dragOffsetToAppCell,
      tryToFreePlace,
      setAppointmentBlocks,
      dragStartScrollY,
      getDaySchedule,
      onDraggingApart,
    ]
  );

  const mouseMoveHandler = useCallback(
    async (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      if (!isDragging && addAppButtonOn) {
        // console.log("move no dragging");
        if (isScrollingInitialized) return;

        const { pageX, pageY } = event;
        const { column, row, outOfBoundary } = getRowColumn(
          pageX,
          pageY,
          Math.floor
        );

        if (outOfBoundary) {
          if (addAppButtonVisible) setVisible(false);

          return;
        }

        const date = await getDate(column);
        const app = getApp(date, row);

        if (app) return;

        const daySchedule = await getDaySchedule(date);

        // check if column is restricted
        const zones = daySchedule.restrictedZones;

        const restricted = zones.some((zone) =>
          withinInterval(date, zone, true, false)
        );

        if (restricted) return;

        setLevel(row);
        setDate(date);
        setVisible(true);

        // setActiveAppId(app.id);

        return;
      }

      const relativeTriggerLeft = event.clientX;
      const relativeTriggerRight = window.innerWidth - relativeTriggerLeft;

      const relativeTriggerTop = event.clientY;
      const relativeTriggerBottom = window.innerHeight - relativeTriggerTop;

      setRelativeTriggerLeft(relativeTriggerLeft);
      setRelativeTriggerRight(relativeTriggerRight);
      setRelativeTriggerTop(relativeTriggerTop);
      setRelativeTriggerBottom(relativeTriggerBottom);
    },
    [
      isDragging,
      setLevel,
      setDate,
      setVisible,
      addAppButtonVisible,
      getDate,
      getApp,
      getRowColumn,
      getDaySchedule,
      isScrollingInitialized,
      addAppButtonOn,
    ]
  );

  // console.log(selfData);

  const innerJSX = useMemo(
    () =>
      initialized ? (
        <>
          <ScrollingLayer />
          <StaticLayer />
        </>
      ) : (
        "loading"
      ),
    [initialized]
  );

  const height = rowHeight * (rowsCount + 2 + levelBlocks.length) + 1;

  const [width, setWidth] = useState<number | string>("auto");
  const [rebuilding, setRebulding] = useState(true);
  const rebuldingTimeout = useRef(0);

  // console.log({ rebuilding });

  useLayoutEffect(() => {
    if (rebuilding) {
      setWidth("auto");

      clearTimeout(rebuldingTimeout.current);
      rebuldingTimeout.current = window.setTimeout(
        () => setRebulding(false),
        1000
      );
    } else setWidth(columnWidth * columnsCount + 2);
  }, [rebuilding]);

  useEffect(() => {
    const wrapperWidth = (self.current as HTMLDivElement).getBoundingClientRect()
      .width;
    if (loaded && width === "auto") setWrapperWidth(wrapperWidth);
  }, [width, loaded, setWrapperWidth, visibleLevelsBlock]);

  const windowWidthDPrev = usePrevious(windowWidthD);
  useEffect(() => {
    if (windowWidthDPrev !== windowWidthD && !rebuilding) setRebulding(true);
  }, [windowWidthDPrev, windowWidthD, rebuilding]);

  useEffect(() => {
    setRebulding(true);
  }, [visibleLevelsBlock]);

  return useMemo(
    () => (
      <div
        styleName="mainBlock"
        ref={self}
        onMouseDownCapture={mouseDownHandler}
        onMouseUpCapture={mouseUpHandler}
        onMouseMoveCapture={mouseMoveHandler}
        onDropCapture={mouseUpHandler}
        style={{ height, width }}
      >
        {innerJSX}
      </div>
    ),
    [
      mouseDownHandler,
      mouseUpHandler,
      mouseMoveHandler,
      innerJSX,
      height,
      width,
    ]
  );
};

export default MainBlock;
