import { addDays, differenceInDays, startOfDay } from 'date-fns';

import { IntervalInt, intervalsSubstract, numberizeInterval } from '../assembly/interval';
import { DaySchedule } from '../components/Calendar/containers/SchedulesContainer';

export default function getNonRestrictedIntervals(
  schedules: DaySchedule[]
): Array<{ schedule: DaySchedule; intervals: IntervalInt[] }> {
  return schedules.map(schedule => {
    const { dayInterval: interval, restrictedZones } = schedule;

    const zones: Interval[] = restrictedZones.map(zone => {
      const dayOffset = differenceInDays(
        startOfDay(interval.start),
        startOfDay(zone.start)
      );
      return {
        start: addDays(zone.start, dayOffset),
        end: addDays(zone.end, dayOffset)
      };
    });

    const intervalsSplitted = zones
      .reduce(
        (acc: Interval[], val: Interval) =>
          acc.reduce(
            (acc2: Interval[], val2) => [
              ...acc2,
              ...intervalsSubstract(val2, val)
            ],
            []
          ),
        [interval]
      )
      .map(numberizeInterval);

    return { intervals: intervalsSplitted, schedule };
  });
}
