import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { ColumnsContainer } from './ColumnsContainer';
import { OrientationContainer } from './OrientationContainer';
import { RowsContainer } from './RowsContainer';

export const TimeLineContainer = createContainer(() => {
  const { rowHeight } = RowsContainer.useContainer();
  const { columnWidth } = ColumnsContainer.useContainer();
  const { horizontal } = OrientationContainer.useContainer();

  const timeLineSize = horizontal ? rowHeight : columnWidth;

  return { timeLineSize };
});
