import './BackGrid.scss';

import React, { useMemo } from 'react';

import { ColumnsContainer } from '../../../containers/ColumnsContainer';
import { LevelBlocksContainer } from '../../../containers/LevelBlocksContainer';
import { RowsContainer } from '../../../containers/RowsContainer';
import { ScrollControlContainer } from '../../../containers/ScrollControlContainer';
import { SettingsBackGridContainer } from '../../../containers/SettingsBackGridContainer';
import { ViewIntervalPixelsContainer } from '../../../containers/ViewIntervalPixelsContainer';

const BackGrid = () => {
  const { columnWidth, columnsCount } = ColumnsContainer.useContainer();
  const { rowsCount, rowHeight } = RowsContainer.useContainer();
  const { oneScrollPixels } = ScrollControlContainer.useContainer();
  const {
    viewIntervalPixelsWidth,
    viewIntervalPixelsPending
  } = ViewIntervalPixelsContainer.useContainer();
  const { levelBlocks } = LevelBlocksContainer.useContainer();
  const { visibileHorizontalLines } = SettingsBackGridContainer.useContainer();

  const buffer = oneScrollPixels;

  const additionalColumnsOneSide = Math.ceil(buffer / columnWidth) || 0;

  const rows = useMemo(
    () =>
      !visibileHorizontalLines
        ? []
        : new Array(rowsCount + levelBlocks.length).fill(null).map((_, i) => (
            <div
              key={i}
              styleName="border horizontal"
              style={{
                height: rowHeight,
                transform: `translate(${-buffer}px, ${i * rowHeight}px)`,
                width: buffer * 2 + viewIntervalPixelsWidth || 0
              }}
            />
          )),
    [
      visibileHorizontalLines,
      viewIntervalPixelsWidth,
      rowsCount,
      buffer,
      rowHeight,
      levelBlocks
    ]
  );

  const columns = useMemo(
    () =>
      new Array(columnsCount + additionalColumnsOneSide * 2 + 2)
        .fill(null)
        .map((_, i) => (
          <div
            key={i}
            styleName="border vertical"
            style={{
              transform: `translate(${(i - additionalColumnsOneSide - 1) *
                columnWidth}px, 0px)`,
              width: columnWidth
            }}
          />
        )),
    [columnsCount, columnWidth, additionalColumnsOneSide]
  );

  const translateX: number = viewIntervalPixelsPending.start;
  const translate = `translate(${translateX}px, ${0}px)`;

  return useMemo(
    () => (
      <div styleName="backGrid" style={{ transform: translate }}>
        {columns}
        {rows}
      </div>
    ),
    [translate, columns, rows]
  );
};

export default BackGrid;
