import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { OrientationContainer } from './OrientationContainer';

export enum Orientation {
  Horizontal,
  Vertical
}

export const WrapperContainer = createContainer(() => {
  const { horizontal } = OrientationContainer.useContainer();

  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);

  const sizePixelsTimeDimension = horizontal ? width : height;
  const sizePixelsLevelsDimension = horizontal ? height : width;

  // console.log({
  //   width,
  //   height,
  //   sizePixelsTimeDimension,
  //   sizePixelsLevelsDimension
  // });

  return {
    width,
    height,
    setWidth,
    setHeight,
    sizePixelsLevelsDimension,
    sizePixelsTimeDimension
  };
});
