import { useEffect, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../../../assembly/interval';
import { ColumnsContainer } from './ColumnsContainer';
import { DaysOffsetsContainer } from './DaysOffsetsContainer';
import { UIScrollStateContainer } from './UIScrollStateContainer';
import { ViewIntervalPixelsContainer } from './ViewIntervalPixelsContainer';
import { ViewIntervalContainer } from './ViewIntevalContainer';

export const ScrollControlContainer = createContainer(() => {
  const { offsetToDate } = DaysOffsetsContainer.useContainer();
  const {
    scrollToNewViewInterval,
    isScrollingInitialized,
  } = ViewIntervalContainer.useContainer();
  const {
    viewIntervalPixels,
    viewIntervalPixelsWidth,
    isScrollingInProgress,
  } = ViewIntervalPixelsContainer.useContainer();
  const { isScrollingUI } = UIScrollStateContainer.useContainer();
  const { columnWidth } = ColumnsContainer.useContainer();

  const scrollDuration = 500;
  const scrollRate = 0.4;
  const oneScrollColumns =
    Math.floor((viewIntervalPixelsWidth * scrollRate) / columnWidth) || 0;
  const oneScrollPixels = oneScrollColumns * columnWidth;

  const isScrolling =
    isScrollingInitialized || isScrollingInProgress || isScrollingUI;

  // console.log({
  //   isScrollingInitialized,
  //   isScrollingInProgress,
  //   isScrollingUI
  // });
  const scrollByPixelsDelta = async (delta: number) => {
    // console.log("try scroll");
    // console.log({
    //   isScrollingInitialized,
    //   isScrollingInProgress,
    //   isScrollingUI
    // });
    if (isScrolling) return;

    console.log("scroll by pixels delta");

    const viewIntervalPixelsNew: IntervalInt = {
      start: viewIntervalPixels.start + delta,
      end: viewIntervalPixels.end + delta,
    };
    const viewIntervalNew: IntervalInt = {
      start: await offsetToDate(viewIntervalPixelsNew.start),
      end: await offsetToDate(viewIntervalPixelsNew.end),
    };

    scrollToNewViewInterval(viewIntervalNew, scrollDuration);
  };

  const scrollLeft = () => {
    scrollByPixelsDelta(-1 * oneScrollPixels);
  };

  const scrollRight = () => {
    scrollByPixelsDelta(oneScrollPixels);
  };

  const scrollReset = () => {
    scrollByPixelsDelta(-1 * viewIntervalPixels.start);
  };

  return {
    scrollLeft,
    scrollRight,
    scrollReset,
    scrollDuration,
    oneScrollPixels,
    oneScrollColumns,
    isScrolling,
  };
});
