import { useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { ColumnsContainer } from './ColumnsContainer';
import { DaysOffsetsContainer } from './DaysOffsetsContainer';
import { LevelBlocksContainer } from './LevelBlocksContainer';
import { RowsContainer } from './RowsContainer';
import { SettingsAddAppButtonContainer } from './SettingsAddAppButtonContainer';
import { SettingsDividersContainer } from './SettingsDivideresContainer';

export const AddAppButtonContainer = createContainer(() => {
  // const { horizontal } = OrientationContainer.useContainer();
  const { columnWidth } = ColumnsContainer.useContainer();
  const { rowHeight } = RowsContainer.useContainer();
  const { dateToOffset } = DaysOffsetsContainer.useContainer();
  const { levelBlocks } = LevelBlocksContainer.useContainer();
  const { dividerUnderBlock } = SettingsDividersContainer.useContainer();
  const { addAppButtonOn } = SettingsAddAppButtonContainer.useContainer();

  const [date, setDate] = useState(Date.now());
  const [level, setLevel] = useState(-1);
  const [visible, setVisible] = useState(false);

  const x = useMemo(() => dateToOffset(date), [dateToOffset, date]); // PERFORMANCE_LEAK: we can remove dateToOffset from deps
  const y = useMemo(() => {
    const levelBlocksPast = Math.max(
      levelBlocks.filter(
        block => (dividerUnderBlock ? block.end : block.start) <= level
      ).length,
      dividerUnderBlock ? 0 : 1
    );

    return (level + levelBlocksPast + 1) * rowHeight;
  }, [level, rowHeight, levelBlocks, dividerUnderBlock]);

  return {
    visible: visible && addAppButtonOn,
    x,
    y,
    width: columnWidth,
    height: rowHeight,
    setDate,
    setLevel,
    setVisible
  };
});
