import useCookie from '@devhammed/use-cookie';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { DraggingContainer } from './DraggingContainer';
import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsDraggingContainer = createContainer(() => {
  const {
    onDragging,
    onDraggingApart,
    setOnDragging,
    setOnDraggingApart,
  } = DraggingContainer.useContainer();

  const [onDraggingStored, setOnDraggingStored] = useCookie(
    "onDragging",
    onDragging
  );
  const [onDraggingApartStored, setOnDraggingApartStored] = useCookie(
    "onDraggingApart",
    onDraggingApart
  );

  useEffect(() => {
    if (onDraggingStored !== onDragging) setOnDragging(onDraggingStored);
  }, [onDraggingStored, setOnDragging, onDragging]);

  useEffect(() => {
    if (onDraggingApartStored !== onDraggingApart)
      setOnDraggingApart(onDraggingApartStored);
  }, [onDraggingApartStored, setOnDraggingApart, onDraggingApart]);

  const setNewValueOnDragging = useCallback(
    (value: boolean) => {
      setOnDragging(value);
      setOnDraggingStored(value);
    },
    [setOnDragging, setOnDraggingStored]
  );

  const setNewValueOnDraggingApart = useCallback(
    (value: boolean) => {
      setOnDraggingApart(value);
      setOnDraggingApartStored(value);
    },
    [setOnDraggingApart, setOnDraggingApartStored]
  );

  const onDraggingSetting: SettingChunk = useMemo(
    () => ({
      type: SettingType.Checkbox,
      name: "Dragging",
      checked: onDragging,
      setChecked: setNewValueOnDragging,
    }),
    [onDragging, setNewValueOnDragging]
  );

  const onDraggingApartSetting: SettingChunk = useMemo(
    () => ({
      type: SettingType.Checkbox,
      name: "Dragging Apart",
      checked: onDraggingApart,
      disabled: !onDragging,
      setChecked: setNewValueOnDraggingApart,
    }),
    [onDraggingApart, onDragging, setNewValueOnDraggingApart]
  );

  return {
    onDraggingSetting,
    onDraggingApartSetting,
  };
});
