let isSticky = false;

interface StickyElement {
  element: HTMLElement;
  fixHorizontally?: boolean;
  id: string;
  stickyCallback: (isSticky: boolean) => void;
}

function makeElementsSticky(elements: StickyElement[][]): void {
  console.log("make sticky");
  elements.reduce((offsetTop: number, elements) => {
    elements.forEach(({ element, fixHorizontally, stickyCallback }) => {
      if (fixHorizontally) {
        console.log(element.getBoundingClientRect().left);
        element.style.left = `${element.getBoundingClientRect().left}px`;
      }

      element.style.top = `${offsetTop}px`;
      stickyCallback(true);
    });

    return offsetTop + elements[0].element.offsetHeight;
  }, 0);

  elements.forEach((elements) =>
    elements.forEach(({ element }) => (element.style.position = "fixed"))
  );

  isSticky = true;
}

function makeElementsNonSticky(elements: StickyElement[][]): void {
  elements.flat().forEach(({ element, stickyCallback }) => {
    element.style.position = "";
    element.style.top = "";
    element.style.left = "";
    stickyCallback(false);
  });

  isSticky = false;
}

export const StickyController = (function() {
  const elements: StickyElement[][] = [];
  let triggerScrollValue: number | null = null;

  window.addEventListener("scroll", () => {
    if (triggerScrollValue === null) return;

    const { scrollY } = window;
    if (scrollY >= triggerScrollValue) {
      if (!isSticky) makeElementsSticky(elements);
    } else if (isSticky) makeElementsNonSticky(elements);
  });

  return {
    addStickyElement: (
      element: HTMLElement,
      index: number,
      id: string,
      fixHorizontally = false,
      stickyCallback = (isSticky: boolean) => {}
    ) => {
      if (!elements[index]) elements[index] = [];

      const existingIndex = elements[index].findIndex((el) => el.id === id);
      if (existingIndex !== -1) elements[index].splice(existingIndex, 1);
      elements[index].push({ element, fixHorizontally, id, stickyCallback });
    },
    setTriggerScrollValue: (value: number) =>
      isSticky ? null : (triggerScrollValue = value),
  };
})();
