import './Dividers.scss';

import React from 'react';

import { LevelBlocksContainer } from '../../../containers/LevelBlocksContainer';
import { RowsContainer } from '../../../containers/RowsContainer';
import { SettingsDividersContainer } from '../../../containers/SettingsDivideresContainer';

const Dividers = () => {
  const { levelBlocks } = LevelBlocksContainer.useContainer();
  const { rowHeight } = RowsContainer.useContainer();
  const { dividerUnderBlock } = SettingsDividersContainer.useContainer();

  return (
    <div styleName="dividers">
      {levelBlocks.map((block, i) => {
        const y =
          ((dividerUnderBlock ? block.end : block.start) + i + 1) * rowHeight;
        return (
          <div
            styleName="block"
            key={block.name}
            style={{ top: y, height: rowHeight }}
          >
            <div styleName="wrapper">
              <span styleName="label">{block.name}</span>
              <span styleName="info">{block.info}</span>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Dividers;
