import './NfcCardBlock.scss';

import React, { useCallback } from 'react';

import { User } from '../../../../../stores/users';
import Button from '../../../../common/Button';

export interface NfcCardBlockProps {
  user: User;
}

const NfcCardBlock = ({ user }: NfcCardBlockProps) => {
  const { nfc } = user;

  const onClickDeleteButton = useCallback(() => null, []);
  const onClickAssignButton = useCallback(() => null, []);

  return (
    <div styleName="nfcCard">
      <div styleName="status">
        {nfc ? "NFC card is assigned" : "no NFC card assigned"}
      </div>
      {nfc ? (
        <Button onClick={onClickDeleteButton} wrapper="div">
          Delete NFC card
        </Button>
      ) : (
        <Button onClick={onClickAssignButton} wrapper="div">
          Assign NFC card
        </Button>
      )}
    </div>
  );
};

export default NfcCardBlock;
