import './LevelsBlock.scss';

import classnames from 'classnames';
import React, { useMemo } from 'react';

import { LevelBlocksContainer } from '../containers/LevelBlocksContainer';
import { RowsContainer } from '../containers/RowsContainer';

const LevelsBlock = () => {
  const { rowHeight } = RowsContainer.useContainer();
  const { indexesMap } = LevelBlocksContainer.useContainer();

  return useMemo(
    () => (
      <div styleName="levelsBlock">
        {indexesMap.map((index, i) => {
          const isBlank = indexesMap[i - 1] === index;
          return (
            <span
              styleName={classnames(
                "chunk",
                i === 0 ? "first" : "",
                isBlank ? "blank" : ""
              )}
              key={i}
              style={{ height: rowHeight }}
            >
              {index - 1}
            </span>
          );
        })}
      </div>
    ),
    [rowHeight, indexesMap]
  );
};

export default LevelsBlock;
