import './TopBlock.scss';

import React, { Ref, useEffect, useLayoutEffect, useRef, useState } from 'react';

import { StickyController } from '../../../assembly/stickyController';
import Striped from '../../common/Striped';
import { OrientationContainer } from '../containers/OrientationContainer';
import { SettingsTopBlockLayoutContainer } from '../containers/SettingsTopBlockLayoutContainer';
import MonthChunkToday from './MonthChunkToday';
import MonthLabel from './MonthLabel';
import MonthRow from './MonthRow';

const TopBlock = (_: any, ref: Ref<HTMLDivElement>) => {
  const { vertical } = OrientationContainer.useContainer();
  const {
    layoutTypeStandart,
    layoutTypeSticky
  } = SettingsTopBlockLayoutContainer.useContainer();

  const [isSticky, setIsSticky] = useState(false);

  const self = useRef<HTMLDivElement>(null);

  // sticky hook
  useLayoutEffect(() => {
    const element = self.current as HTMLDivElement;

    const stickyCallback = (isStickyNew: boolean) => setIsSticky(isStickyNew);

    StickyController.addStickyElement(
      element,
      0,
      "topBlock",
      false,
      stickyCallback
    );
    StickyController.setTriggerScrollValue(element.offsetTop);
    // }, []);
  }, []);

  return vertical ? (
    <div styleName="topBlock" ref={ref}>
      <div styleName="content" ref={self}>
        <Striped />
      </div>
    </div>
  ) : (
    <div styleName="topBlock" ref={ref}>
      <div styleName="content" ref={self}>
        <MonthLabel />
        <MonthChunkToday />
        <MonthRow />
      </div>
    </div>
  );
};

export default React.forwardRef(TopBlock);
