import './AddAppButton.scss';

import React from 'react';

import { AddAppButtonContainer } from '../../../containers/AddAppButtonContainer';

const AddAppButton = () => {
  const { x, y, width, height, visible } = AddAppButtonContainer.useContainer();

  return (
    <div
      styleName="addAppButton"
      style={{
        left: x,
        top: y,
        width,
        height,
        visibility: visible ? "visible" : "hidden"
      }}
    ></div>
  );
};

export default AddAppButton;
