import { addDays, differenceInDays, differenceInHours, startOfDay } from 'date-fns';
import uuid from 'uuid';

import { areIntervalsIntersects, IntervalInt } from '../assembly/interval';
import { oneDayDuration } from '../assembly/oneDay';
import { pickRandom, randomInt } from '../assembly/random';
import { shell } from '../assembly/utility';
import getNonRestrictedIntervals from '../mockers/getNonRestrictedIntervals';
import { Event } from '../stores/events';
import { fetchSchedule } from './fetchSchedule';

const backendEvents = new Map<number, Event[]>();

function generateEvent(
  interval: IntervalInt,
  minDuration = oneDayDuration(0, 50),
  maxDuration = oneDayDuration(1, 30)
): Event {
  const start = randomInt(interval.start, interval.end - minDuration);

  const durationRaw = randomInt(minDuration, maxDuration);
  const end = Math.min(start + durationRaw, interval.end);

  return {
    block: randomInt(0, 10),
    id: uuid.v4(),
    interval: { start, end },
    userIds: [],
    duration: end - start,
    headUserId: uuid.v4(),
    name: pickRandom(["Yoga", "Flex", "Cardio"]),
  };
}

function fetchEventsAtInterval(
  interval: IntervalInt,
  eventsPerHour = 10,
  eventsPerHourVariation = 5
): Event[] {
  const hours = differenceInHours(interval.end, interval.start);

  const variation = randomInt(-eventsPerHourVariation, eventsPerHourVariation);
  const eventsToCreate = (eventsPerHour + variation) * hours;
  console.log(eventsToCreate);

  const newEventsRaw = shell(eventsToCreate).map(() => generateEvent(interval));
  const newEvents = newEventsRaw.reduce(
    (acc: Event[], event) =>
      acc.every(
        (event2) =>
          event.id === event2.id ||
          event.block !== event2.block ||
          !areIntervalsIntersects(event.interval, event2.interval)
      )
        ? acc.concat([event])
        : acc,
    []
  );

  return newEvents;
}

export async function fetchEvents(
  intervalFetch: IntervalInt
): Promise<Event[]> {
  const viewIntervalToDays: Interval = {
    start: startOfDay(intervalFetch.start),
    end: addDays(startOfDay(intervalFetch.end), 1),
  };
  const daysCount = differenceInDays(
    startOfDay(viewIntervalToDays.end),
    startOfDay(viewIntervalToDays.start)
  );

  if (!daysCount) return [];

  // console.log({ daysCount });
  const schedules = await Promise.all(
    shell(daysCount).map(async (_, i) => {
      const start = addDays(viewIntervalToDays.start, i);
      const daySchedule = await fetchSchedule(start);
      return daySchedule;
    })
  );

  const intervals = getNonRestrictedIntervals(schedules);

  const result = (
    await Promise.all(
      intervals.map(async ({ intervals, schedule }) => {
        const dayId = startOfDay(schedule.dayInterval.start).valueOf();
        if (!backendEvents.has(dayId))
          backendEvents.set(
            dayId,
            intervals.map((interval) => fetchEventsAtInterval(interval)).flat()
          );

        const dayEvents = backendEvents.get(dayId) as Event[];
        const events = await Promise.all(
          intervals.map((interval) =>
            dayEvents.filter((event) =>
              areIntervalsIntersects(event.interval, interval)
            )
          )
        );

        return events.flat();
      })
    )
  ).flat();

  return result;
}
