import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { Appointment } from '../../../stores/appointments';

export const DraggingContainer = createContainer(() => {
  const [draggingApp, setDraggingApp] = useState<Appointment | null>(null);
  const [dragOffsetToAppCell, setDragOffsetToAppCell] = useState({
    dx: 0,
    dy: 0
  });
  const isDragging = draggingApp !== null;

  const [onDragging, setOnDragging] = useState(true);
  const [onDraggingApart, setOnDraggingApart] = useState(true);

  return {
    onDragging,
    setOnDragging,
    onDraggingApart,
    setOnDraggingApart,
    isDragging,
    draggingApp,
    setDraggingApp,
    dragOffsetToAppCell,
    setDragOffsetToAppCell
  };
});
