import './MonthChunk.scss';

import classnames from 'classnames';
import { format } from 'date-fns';
import React from 'react';

export interface PropsMonthChunk {
  date: number;
  appsCount: number;
  solid?: boolean;
  todayMode?: boolean;
  setCurrentDate: (axis: number) => void;
}

const MonthChunk = ({
  date,
  appsCount,
  solid,
  setCurrentDate,
  todayMode
}: PropsMonthChunk) => {
  const onClick = () => setCurrentDate(date);
  return (
    <span
      styleName={classnames(
        "monthChunk",
        solid ? "solid" : "",
        todayMode ? "todayMode" : ""
      )}
      onClick={onClick}
    >
      <div styleName="weekday">{format(date, "EEEEEE")}</div>
      <div styleName="monthday">{format(date, "d")}</div>
      <div styleName="appsCount">{appsCount}</div>
    </span>
  );
};

export default React.memo(MonthChunk);
