import { useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { WrapperContainer } from './WrapperContainer';

export const ColumnsContainer = createContainer(
  (
    { minColumnWidthInitial }: { minColumnWidthInitial: number } = {
      minColumnWidthInitial: 200
    }
  ) => {
    const { width } = WrapperContainer.useContainer();
    const [minColumnWidth, setMinColumnWidth] = useState(minColumnWidthInitial);

    const { columnWidth, columnsCount } = useMemo(() => {
      const columnsCount = Math.floor(width / minColumnWidth);
      const columnWidth =
        columnsCount === 0 ? 0 : Math.floor(width / columnsCount);

      return { columnsCount, columnWidth };
    }, [width, minColumnWidth]);

    // console.log({ columnWidth, columnsCount });

    return { minColumnWidth, columnWidth, columnsCount, setMinColumnWidth };
  }
);
