import './LabeledInput.scss';

import React, { useLayoutEffect } from 'react';

import InputNumber from '../../../../common/InputNumber';
import InputText from '../../../../common/InputText';

const LabeledInput = ({
  type,
  label,
  onChange,
  value,
  setValidity,
  validator = (val: string) => !isNaN(parseFloat(val)),
  labelInside,
  onBlur
}: {
  type: "text" | "number";
  label: string;
  value: string | number;
  onBlur?: () => void;
  onChange: (val: string) => void;
  validator?: (val: string) => boolean;
  setValidity: (key: string, val: boolean) => void;
  labelInside?: boolean;
}) => {
  const isValid = validator(value.toString());

  useLayoutEffect(() => setValidity(label, isValid), [isValid]);

  const inputEl =
    type === "text" ? (
      <InputText
        onChange={e => onChange(e.target.value)}
        value={value}
        placeholder={label}
        wrapper="div"
        invalid={!isValid}
        onBlur={onBlur}
      />
    ) : (
      <InputNumber
        wrapper="div"
        onChange={e => onChange(e.target.value)}
        value={value}
        invalid={!isValid}
        onBlur={onBlur}
      />
    );

  return labelInside ? (
    inputEl
  ) : (
    <>
      <label>{label}</label>
      {inputEl}
    </>
  );
};

export default LabeledInput;
