import useCookie from '@devhammed/use-cookie';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { ColumnsContainer } from './ColumnsContainer';
import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsColumnContainer = createContainer(() => {
  const { minColumnWidth, setMinColumnWidth } = ColumnsContainer.useContainer();
  const [minColumnWidthStored, setMinColumnWidthStored] = useCookie(
    "minColumnWidth",
    minColumnWidth
  );

  useEffect(() => {
    if (minColumnWidth !== minColumnWidthStored)
      setMinColumnWidth(minColumnWidthStored);
  }, [minColumnWidth, minColumnWidthStored, setMinColumnWidth]);

  const setNewValue = useCallback(
    (value: number) => {
      setMinColumnWidth(value);
      setMinColumnWidthStored(value);
    },
    [setMinColumnWidth, setMinColumnWidthStored]
  );

  const minColumnWidthSetting: SettingChunk = useMemo(
    () => ({
      type: SettingType.Range,
      name: "Min Column Width",
      min: 150,
      max: 300,
      value: minColumnWidth,
      setNewValue,
    }),
    [minColumnWidth, setNewValue]
  );

  return {
    minColumnWidthSetting,
  };
});
