import './TimeLine.scss';

import { format, roundToNearestMinutes } from 'date-fns';
import React, { useMemo, useRef, useState } from 'react';

import { ColumnsContainer } from '../../../containers/ColumnsContainer';
import { DaysOffsetsContainer } from '../../../containers/DaysOffsetsContainer';
import { ScrollControlContainer } from '../../../containers/ScrollControlContainer';
import { TimeLineContainer } from '../../../containers/TimeLineContainer';
import { ViewIntervalPixelsContainer } from '../../../containers/ViewIntervalPixelsContainer';

const TimeLine = () => {
  const { timeLineSize } = TimeLineContainer.useContainer();
  const { columnsCount, columnWidth } = ColumnsContainer.useContainer();
  const { offsetToDate } = DaysOffsetsContainer.useContainer();
  const {
    oneScrollColumns,
    oneScrollPixels
  } = ScrollControlContainer.useContainer();
  const {
    viewIntervalPixels,
    zeroAxisIsUpdating
  } = ViewIntervalPixelsContainer.useContainer();

  const [columnElements, setColumnElements] = useState<JSX.Element[]>([]);
  const lastUpdateTimeStamp = useRef(Date.now());
  const columnsCountBuffered = columnsCount + oneScrollColumns * 2;

  const scrolledColumns =
    (viewIntervalPixels.start / oneScrollPixels) * oneScrollColumns || 0;

  useMemo(async () => {
    if (zeroAxisIsUpdating) return;

    const timeStamp = Date.now();
    const elements = await Promise.all(
      new Array(columnsCountBuffered).fill(null).map(async (_, i) => {
        const iReal = i - oneScrollColumns;
        // const scrolledColumns = 0;
        const currentColumn = scrolledColumns + iReal;
        const currentOffsetPixels = currentColumn * columnWidth;

        const timestamp = await offsetToDate(Math.ceil(currentOffsetPixels));
        const isDayStart = false;

        return (
          <span
            key={i}
            styleName="timeMark"
            style={{
              width: columnWidth,
              background: isDayStart ? "lightgray" : "white"
            }}
          >
            <span styleName="text">
              {format(roundToNearestMinutes(timestamp), "H:mm")}
            </span>
          </span>
        );
      })
    );

    if (timeStamp > lastUpdateTimeStamp.current) {
      lastUpdateTimeStamp.current = timeStamp;
      setColumnElements(elements);

      // console.log(`set ${elements.length}`);
    }

    // PERFORMANCE_LEAK: we can calculate only actual timeline elements
    // else console.log("abort setting -> timeStamp is outdated");
  }, [
    columnsCountBuffered,
    columnWidth,
    oneScrollColumns,
    offsetToDate,
    scrolledColumns,
    zeroAxisIsUpdating
  ]);

  // console.log(
  //   columnsCountBuffered,
  //   "=",
  //   columnsCount,
  //   "+",
  //   oneScrollColumns * 2
  // );
  // console.log(scrollOffsetDone, oneScrollSizePixels);

  const translateX = viewIntervalPixels.start - oneScrollPixels;

  return useMemo(
    () => (
      <div
        styleName="timeLine"
        style={{
          transform: `translateX(${translateX}px)`,
          height: timeLineSize
        }}
      >
        {columnElements}
      </div>
    ),
    [timeLineSize, translateX, columnElements]
  );
};

export default TimeLine;
