import './MonthLabel.scss';

import { format } from 'date-fns/esm';
import React, { useEffect, useMemo, useRef } from 'react';

import { ViewIntervalContainer } from '../../containers/ViewIntevalContainer';

const MonthLabel = () => {
  const { viewInterval } = ViewIntervalContainer.useContainer();
  const currentDate = format(viewInterval.start, "MMMM, do");
  const [month, day] = currentDate.split(" ");

  return useMemo(
    () => (
      <div styleName="monthLabel">
        <span styleName="month">{month}</span>
        <span styleName="month">{day}</span>
      </div>
    ),
    [month, day]
  );
};

export default MonthLabel;
