import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsDividersContainer = createContainer(() => {
  const [dividerUnderBlock, setDividerUnderBlock] = useCookie(
    "dividerUnderBlock",
    true
  );

  const dividerUnderBlockSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: dividerUnderBlock,
    name: "Divider under-block",
    setChecked: setDividerUnderBlock,
  };

  return { dividerUnderBlock, dividerUnderBlockSetting };
});
