import { Map } from 'immutable';
import { useCallback, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../assembly/interval';

export interface Event {
  id: string;
  userIds: string[];
  headUserId: string;
  interval: IntervalInt;
  duration: number;
  block: number;
  name: string;
}

export const EventsContainer = createContainer(() => {
  const [events, setEvents] = useState(Map<string, Event>());

  const eventsActual = useRef(events);
  eventsActual.current = events;

  // console.log(eventsActual.current.size);

  const addEvent = useCallback(
    (event: Event) => setEvents(eventsActual.current.set(event.id, event)),
    []
  );

  const addEvents = useCallback(
    (events: Event[]) =>
      setEvents(
        eventsActual.current.merge(
          Map<string, Event>().withMutations(map =>
            events.reduce(
              (acc: Map<string, Event>, event: Event) =>
                acc.set(event.id, event),
              map
            )
          )
        )
      ),
    []
  );

  const getEvent = useCallback((id: string): Event | undefined => {
    if (eventsActual.current.has(id)) return eventsActual.current.get(id);
  }, []);

  return { addEvent, addEvents, getEvent, events };
});
