import './ControlBlock.scss';

import React, { useLayoutEffect, useRef } from 'react';

import { StickyController } from '../../../assembly/stickyController';
import AppEditor from './AppEditor';
import InfoBlock from './InfoBlock';

const ControlBlock = () => {
  const self = useRef<HTMLDivElement>(null);

  // sticky hook
  useLayoutEffect(() => {
    const element = self.current as HTMLDivElement;

    StickyController.addStickyElement(element, 2, "controlBlock", true);
    // StickyController.setTriggerScrollValue(element.offsetTop);
    // }, []);
  }, []);

  return (
    <div styleName="controllBlock" ref={self}>
      <InfoBlock />
      <AppEditor />
    </div>
  );
};

export default ControlBlock;
