import { useLayoutEffect, useMemo, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { extendIntervalBy, maxInterval } from '../../../assembly/interval';
import { DaysOffsetsContainer } from './DaysOffsetsContainer';
import { InitializeStateContainer } from './InitializeStateContainer';
import { ScrollControlContainer } from './ScrollControlContainer';
import { ViewIntervalPixelsContainer } from './ViewIntervalPixelsContainer';
import { ViewIntervalContainer } from './ViewIntevalContainer';

export const ViewIntervalBufferedContainer = createContainer(() => {
  const { offsetToDate } = DaysOffsetsContainer.useContainer();
  const { viewInterval } = ViewIntervalContainer.useContainer();
  const {
    viewIntervalPixels,
    viewIntervalPixelsPending
  } = ViewIntervalPixelsContainer.useContainer();
  const { oneScrollPixels } = ScrollControlContainer.useContainer();
  const { initialized } = InitializeStateContainer.useContainer();

  const viewIntervalBufferedPixels = useMemo(() => {
    const buffered = extendIntervalBy(
      maxInterval(viewIntervalPixels, viewIntervalPixelsPending),
      oneScrollPixels
    );
    // console.log("viewIntervalPixels:", viewIntervalPixels);
    // console.log("viewIntervalPixelsPending:", viewIntervalPixelsPending);
    // console.log("buffered:", buffered);

    return buffered;
  }, [viewIntervalPixels, viewIntervalPixelsPending, oneScrollPixels]);

  const [viewIntervalBuffered, setViewIntervalBuffered] = useState(
    viewInterval
  );

  const viewIntervalBufferedActual = useRef(viewIntervalBuffered);
  viewIntervalBufferedActual.current = viewIntervalBuffered;

  // useWhyDidYouUpdate("viewIntervalBuffered", {
  //   initialized,
  //   start: viewIntervalBuffered.start,
  //   end: viewIntervalBuffered.end,
  //   viewIntervalBufferedPixels,
  //   offsetToDate,
  //   setViewIntervalBuffered
  // });

  // PERFORMANCE_LEAK: viewIntervalBuffered updates twice per scroll
  useLayoutEffect(() => {
    if (!initialized) return;
    (async () => {
      const start = await offsetToDate(viewIntervalBufferedPixels.start);
      const end = await offsetToDate(viewIntervalBufferedPixels.end);

      if (
        start === viewIntervalBufferedActual.current.start &&
        end === viewIntervalBufferedActual.current.end
      )
        return;

      // console.log("set", {
      //   start,
      //   end
      // });
      setViewIntervalBuffered({
        start,
        end
      });
    })();
  }, [
    initialized,
    viewIntervalBuffered.start,
    viewIntervalBuffered.end,
    viewIntervalBufferedPixels,
    offsetToDate,
    setViewIntervalBuffered
  ]);

  return { viewIntervalBuffered, viewIntervalBufferedPixels };
});
