import { startOfDay } from 'date-fns/esm';
import { useEffect, useMemo, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../../../assembly/interval';
import { SchedulesContainer } from './SchedulesContainer';

export const ViewIntervalContainer = createContainer(
  (
    { viewIntervalInitial }: { viewIntervalInitial: IntervalInt } = {
      viewIntervalInitial: {
        start: startOfDay(Date.now()).valueOf(),
        end: startOfDay(Date.now()).valueOf(),
      },
    }
  ) => {
    const { zeroAxis } = SchedulesContainer.useContainer();

    const [zeroAxisLinked, setZeroAxisLinked] = useState(
      viewIntervalInitial.start
    );

    const zeroAxisIsUpdating = zeroAxis !== zeroAxisLinked;

    const [viewInterval, setViewInterval] = useState(viewIntervalInitial);
    const [viewIntervalPending, setViewIntervalPending] = useState(
      viewInterval
    );
    // const [viewIntervalPixels, setViewIntervalPixels] = useState<IntervalInt>({start: 0, end: 0});

    const isScrollingInitialized = viewInterval !== viewIntervalPending;

    const [scrollJustReset, setScrollJustReset] = useState(false);

    // useEffect(() => {
    //   if (scrollJustReset) setScrollJustReset(false);
    // }, [scrollJustReset]);

    const scrollToNewViewInterval = (
      viewIntervalNew: IntervalInt,
      timeout: number
    ) => {
      if (isScrollingInitialized) return;

      if (scrollJustReset) setScrollJustReset(false);

      setViewIntervalPending(viewIntervalNew);
      setTimeout(() => setViewInterval(viewIntervalNew), timeout);
    };

    const setNewViewIntervalImmediately = (viewIntervalNew: IntervalInt) => {
      setScrollJustReset(true);
      setViewInterval(viewIntervalNew);
      setViewIntervalPending(viewIntervalNew);
    };

    // console.log({ viewInterval, viewIntervalPending });

    // const zeroAxisCurrent = useRef(zeroAxis);
    // const viewIntervalOutput = zeroAxisCurrent.current === zeroAxis ? viewInterval : (schedule.get(startOfDay(zeroAxis).valueOf()) as DaySchedule).dayInterval.start;

    const currentDay: number = useMemo(
      () => startOfDay(viewInterval.start).valueOf(),
      [viewInterval]
    );
    const currentDayPending: number = useMemo(
      () => startOfDay(viewIntervalPending.start).valueOf(),
      [viewIntervalPending]
    );

    if (viewInterval.start > viewInterval.end) debugger;

    return {
      viewInterval,
      viewIntervalPending,
      scrollToNewViewInterval,
      isScrollingInitialized,
      setNewViewIntervalImmediately,
      currentDay,
      currentDayPending,
      zeroAxisLinked,
      setZeroAxisLinked,
      zeroAxisIsUpdating,
      scrollJustReset,
      setScrollJustReset,
    };
  }
);
