import React from 'react';

import GridCell, { GridCellProps } from '../GridCell/GridCell';

export interface GridCellWrapperProps {
  data: GridCellProps;
  x: number;
  y: number;
  width: number;
  height: number;
}

const GridCellWrapper = ({
  data,
  width,
  height,
  y,
  x,
}: GridCellWrapperProps) => {
  return (
    <div
      style={{
        height: height - 1,
        transform: `translate(${x}px, ${y + 1}px)`,
        width,
        position: "absolute",
        borderRadius: height,
      }}
    >
      <GridCell {...data} />
    </div>
  );
};

export default React.memo(GridCellWrapper);
