import { useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../../../assembly/interval';
import { shell } from '../../../assembly/utility';
import { ColumnsContainer } from './ColumnsContainer';
import { OrientationContainer } from './OrientationContainer';
import { RowsContainer } from './RowsContainer';
import { SettingsDividersContainer } from './SettingsDivideresContainer';

export type LevelBlock = IntervalInt & { name: string; info: string };
export const LevelBlocksContainer = createContainer(() => {
  const { horizontal } = OrientationContainer.useContainer();
  const { rowsCount } = RowsContainer.useContainer();
  const { columnsCount } = ColumnsContainer.useContainer();
  const { dividerUnderBlock } = SettingsDividersContainer.useContainer();

  const [levelBlocks, setLevelBlocks] = useState<LevelBlock[]>([
    {
      start: 0,
      end: 2,
      name: "Room 1",
      info: "Yoga: 25, Cardio: 33, Flex: 66",
    },
    {
      start: 3,
      end: 5,
      name: "Room 2",
      info: "Yoga: 25, Cardio: 33, Flex: 66",
    },
    {
      start: 6,
      end: 16,
      name: "Room 3",
      info: "Yoga: 25, Cardio: 33, Flex: 66",
    },
    // {
    //   start: 0,
    //   end: 3,
    //   name: "Room 1",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // },
    // {
    //   start: 4,
    //   end: 5,
    //   name: "Room 2",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // },
    // {
    //   start: 6,
    //   end: 8,
    //   name: "Room 3",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // },
    // {
    //   start: 9,
    //   end: 13,
    //   name: "Room 4",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // },
    // {
    //   start: 14,
    //   end: 36,
    //   name: "Room 5",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // },
    // {
    //   start: 37,
    //   end: 41,
    //   name: "Room 6",
    //   info: "Yoga: 25, Cardio: 33, Flex: 66"
    // }
  ]);

  const levelsCount = horizontal ? rowsCount : columnsCount;

  const indexesMap = useMemo(() => {
    let counter = 0;
    let wasPrevious = false;
    const result = shell(levelsCount + levelBlocks.length + 2).reduce(
      (acc: number[]) => {
        const isStartOfSomething =
          levelBlocks.some(
            (block) => (dividerUnderBlock ? block.end : block.start) === counter
          ) && !wasPrevious;
        wasPrevious = isStartOfSomething;

        const newAcc = [...acc, counter];
        if (!isStartOfSomething) counter++;

        return newAcc;
      },
      []
    );

    return result;
  }, [levelsCount, levelBlocks, dividerUnderBlock]);

  return { levelsCount, levelBlocks, setLevelBlocks, indexesMap };
});
