import './GridCell.scss';

import classnames from 'classnames';
import React, { useEffect, useMemo, useState } from 'react';

import CenteredHorizontally from '../../../../common/CenteredHorizontally';
import CenteredVertically from '../../../../common/CenteredVertically';
import { SettingsAppContainer } from '../../../containers/SettingsAppContainer';

export interface GridCellProps {
  name: string;
  info: string;
  counter: number;
  color: string;
  imgSrc?: string;
}

const GridCell = (props: GridCellProps) => {
  const {
    visibilityAvatar,
    visibilityVisits,
  } = SettingsAppContainer.useContainer();

  const { counter, info, name, color, imgSrc } = props;

  const inner = useMemo(
    () => (
      <>
        <div styleName="leftBlock">
          <span styleName="marker" />
          <CenteredVertically>
            <span styleName="avatar">
              <img styleName="avatarContent" src={imgSrc} />
            </span>
          </CenteredVertically>
          <CenteredVertically>
            <div styleName="textData">
              <span styleName="name">{name}</span>
              <span styleName="info">{info}</span>
            </div>
          </CenteredVertically>
        </div>
        <div styleName="rightBlock">
          <CenteredVertically>
            <span styleName="pointsWrapper">
              <CenteredHorizontally>
                <CenteredVertically>
                  <span styleName="pointsText">{counter}</span>
                </CenteredVertically>
              </CenteredHorizontally>
            </span>
          </CenteredVertically>
        </div>
      </>
    ),
    [name, info, counter, imgSrc]
  );

  return useMemo(
    () => (
      <div
        styleName={classnames(
          "gridCell",
          visibilityAvatar ? "" : "noAvatar",
          visibilityVisits ? "" : "noVisits",
          "noMarker"
        )}
      >
        <div
          styleName="wrapper"
          style={{
            backgroundColor: color,
          }}
        >
          {inner}
        </div>
      </div>
    ),
    [inner, color, visibilityAvatar, visibilityVisits]
  );
};

export default React.memo(GridCell);
