import './MasterButton.scss';

import classnames from 'classnames';
import React, { useCallback, useState } from 'react';

import MasterMenu from './MasterMenu';

export interface MasterButtonProps {
  name: string;
  surname: string;
}

const MasterButton = () => {
  const [masterMenuVisible, setMasterMenuVisible] = useState(true);

  const makeVisible = useCallback(() => setMasterMenuVisible(true), []);
  const makeInvisible = useCallback(() => setMasterMenuVisible(false), []);

  return (
    <div
      styleName={classnames("masterButton", masterMenuVisible ? "active" : "")}
      onMouseEnter={makeVisible}
      onMouseLeave={makeInvisible}
    >
      <div styleName="circle">J.A.</div>
      <div styleName="marker" />
      <MasterMenu visible={masterMenuVisible} />
    </div>
  );
};

export default React.memo(MasterButton);
