import './AboBlock.scss';

import React, { useCallback, useMemo } from 'react';

import { User } from '../../../../../stores/users';
import Button from '../../../../common/Button';

export interface AboBlockProps {
  user: User;
}

const AboBlock = ({ user }: AboBlockProps) => {
  const aboPause = useCallback(() => null, []);
  const aboExtend = useCallback(() => null, []);
  const aboAddNew = useCallback(() => null, []);
  const aboDelete = useCallback(() => null, []);

  const buttons: Array<[() => void, string, boolean?]> = [
    [aboPause, "Pause abo"],
    [aboExtend, "Extend abo"],
    [aboAddNew, "Add new abo"],
    [aboDelete, "Delete abo", true]
  ];

  const inner = useMemo(() => {
    if (!user.abo) return <div styleName="info">no abo</div>;

    const { name, visitsLeft } = user.abo;

    return (
      <>
        <div styleName="info">
          <span styleName="name">{name}</span>
          <span styleName="visitsLeft">visits left: {visitsLeft}</span>
        </div>
        {buttons.map(([onClick, text, danger]) => (
          <Button onClick={onClick} key={text} danger={danger} wrapper="div">
            {text}
          </Button>
        ))}
      </>
    );
  }, [user.abo, buttons]);

  return <div styleName="aboBlock">{inner}</div>;
};

export default AboBlock;
