import { addDays, areIntervalsOverlapping, differenceInDays, startOfDay } from 'date-fns';

import { IntervalInt } from '../assembly/interval';
import { pickRandom, randomInt } from '../assembly/random';
import { timeout } from '../assembly/timeout';
import { shell } from '../assembly/utility';
import getNonRestrictedIntervals from '../mockers/getNonRestrictedIntervals';
import { AppointmentNoUser, AppointmentUnassigned, createAppointment } from '../stores/appointments';
import { User } from '../stores/users';
import { fetchSchedule } from './fetchSchedule';
import { fetchUser, userIds } from './fetchUser';

const processedIntervals = new Map<number, AppointmentNoUser[]>();

export function isIntervalAlreadyFetched(dayIntervals: Interval) {
  return processedIntervals.has(dayIntervals.start.valueOf());
}

export function fetchAppointmentsAtInterval(
  dayInterval: Interval,
  returnAlreadyFetched: boolean,
  toGenerate: number,
  stepDuration: Date | number = 1,
  maxRowIndex = 20
) {
  function snapValue(val: number): number {
    const a = val / stepDuration.valueOf();
    const b = Math.floor(a);
    return b * stepDuration.valueOf();
  }

  const possibleDuration: Interval = {
    end: stepDuration.valueOf() * 2,
    start: stepDuration.valueOf()
  };

  const dayIntervalStart = dayInterval.start.valueOf();
  if (processedIntervals.has(dayIntervalStart))
    if (returnAlreadyFetched)
      return processedIntervals.get(
        dayIntervalStart
      ) as AppointmentUnassigned[];
    else return [];

  const intervalDuration =
    dayInterval.end.valueOf() - dayInterval.start.valueOf();
  const apps = shell(toGenerate)
    .map(() => {
      return (
        dayInterval.start.valueOf() +
        snapValue(
          randomInt(
            0,
            Math.max(0, intervalDuration - possibleDuration.start.valueOf())
          )
        )
      );
    })
    .map(start => {
      const duration = snapValue(
        randomInt(
          possibleDuration.start,
          possibleDuration.end.valueOf() +
            (possibleDuration.end.valueOf() - possibleDuration.start.valueOf())
        )
      );
      const end = Math.min(start + duration, dayInterval.end.valueOf());
      const interval = { start, end };

      // console.log(format(interval.start, "dd HH:mm"));

      return createAppointment({
        interval,
        rowIndex: randomInt(0, maxRowIndex),
        userId: pickRandom(userIds)
      });
    })
    .reduce(
      (acc: AppointmentNoUser[], app) =>
        acc.some(
          app2 =>
            app2.rowIndex === app.rowIndex &&
            areIntervalsOverlapping(app.interval, app2.interval)
        )
          ? acc
          : [...acc, app],
      []
    );

  processedIntervals.set(dayIntervalStart, apps);
  // console.log(
  //   `${apps.length} apps fetched for \n${intervalToString(dayInterval)}`
  // );

  return apps;
}

export async function fetchAppointmentsAtIntervals(
  intervals: Interval[],
  stepDuration: Date | number = 1,
  densityPerDay = 200,
  maxRowIndex = 10,
  returnAlreadyFetched = false
): Promise<AppointmentUnassigned[]> {
  await timeout(0);

  const intervalsFilled = intervals.map(interval => {
    return fetchAppointmentsAtInterval(
      interval,
      returnAlreadyFetched,
      densityPerDay,
      stepDuration,
      maxRowIndex
    );
  });

  const apps = await Promise.all(
    intervalsFilled
      .reduce((acc, val) => [...acc, ...val])
      .map(async app => ({
        ...app,
        user: (await fetchUser(app.userId)) as User
      }))
  );

  return apps;
}

export async function fetchAppointments(
  interval: IntervalInt
): Promise<AppointmentUnassigned[]> {
  const viewIntervalToDays: Interval = {
    start: startOfDay(interval.start),
    end: addDays(startOfDay(interval.end), 1)
  };
  const daysCount = differenceInDays(
    startOfDay(viewIntervalToDays.end),
    startOfDay(viewIntervalToDays.start)
  );

  if (!daysCount) return [];

  const schedules = await Promise.all(
    shell(daysCount).map(async (_, i) => {
      const start = addDays(viewIntervalToDays.start, i);
      const daySchedule = await fetchSchedule(start);
      return daySchedule;
    })
  );

  const intervals = getNonRestrictedIntervals(schedules);

  const apps = (
    await Promise.all(
      intervals.map(async ({ intervals, schedule }) => {
        const apps = await fetchAppointmentsAtIntervals(
          intervals,
          schedule.gridStepDuration,
          70,
          39
        );

        return apps;
      })
    )
  ).reduce((acc, val) => [...acc, ...val]);

  return apps;
}
