import { useEffect, useLayoutEffect, useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { fetchEvents } from '../../fetchers/fetchEvents';
import { Event, EventsContainer } from '../../stores/events';
import { ViewIntervalBufferedContainer } from './containers/ViewIntervalBufferedContainer';

export const AutoFetcherEvents = createContainer(() => {
  const { viewIntervalBuffered } = ViewIntervalBufferedContainer.useContainer();
  const { addEvents } = EventsContainer.useContainer();

  const [nextPusingEvents, setNextPusingEvents] = useState<Event[]>([]);

  // console.log(events.size);

  useLayoutEffect(() => {
    if (nextPusingEvents.length) {
      addEvents(nextPusingEvents);
      setNextPusingEvents([]);
    }
  }, [addEvents, nextPusingEvents, setNextPusingEvents]);

  useMemo(async () => {
    const events = await fetchEvents(viewIntervalBuffered);

    if (events.length) setNextPusingEvents(events);
  }, [viewIntervalBuffered, setNextPusingEvents]);
});
