import './IdInput.scss';

import React, { useCallback } from 'react';

import { User } from '../../../../../../stores/users';
import Button from '../../../../../common/Button';
import { ValidityContainer } from '../../AppEditor';
import LabeledInput from '../../LabeledInput';

export interface IdInputProps {
  value: string;
  onChange: (value: string) => void;
  suggestAutofill: boolean;
  suggestingUsers: User[];
  placeholder: string;
  setCurrentUser: (user: User) => void;
  forcedValid: boolean;
  createNewUser: () => void;
  onBlur: () => void;
}

const IdInput = ({
  forcedValid,
  value,
  onBlur,
  onChange,
  suggestAutofill,
  suggestingUsers,
  placeholder,
  setCurrentUser,
  createNewUser
}: IdInputProps) => {
  const { setValidity } = ValidityContainer.useContainer();

  const onInputChange = useCallback((val: string) => onChange(val), [onChange]);

  return (
    <div styleName="idInput">
      <LabeledInput
        type="text"
        onBlur={onBlur}
        label={placeholder}
        onChange={onInputChange}
        setValidity={setValidity}
        value={value}
        labelInside
        validator={(val: string) => forcedValid || val.length > 0}
      />
      {suggestAutofill ? (
        <>
          <Button
            styleName="addNewClientButton"
            wrapper="div"
            onClick={createNewUser}
          >
            Add new client
          </Button>
          <div styleName="clientsList">
            {suggestingUsers.map(user => (
              <button
                key={user.id}
                styleName="client"
                onClick={() => setCurrentUser(user)}
              >
                {user.name}
              </button>
            ))}
          </div>
        </>
      ) : null}
    </div>
  );
};

export default IdInput;
