import { List, Map } from 'immutable';
import { useCallback, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../assembly/interval';
import { randomInt } from '../assembly/random';
import { UserField } from '../components/Calendar/ControlBlock/AppEditor/AppEditor';
import { fetchUser, postUser } from '../fetchers/fetchUser';
import { fetchUsersStartingWith } from '../fetchers/fetchUsersStartingWith';
import { Appointment, AppointmentsInfo } from './appointments';

export interface Abonnement {
  name: string;
  interval: IntervalInt;
  visitsLeft: number;
}

export interface UserInfo {
  sizes: {
    breast: number | string;
    waist: number | string;
    legs: number | string;
    hands: number | string;
  };
  weight: number | string;
}

export interface User {
  id: string;
  name: string;
  phone: string;
  info: UserInfo;
  abo?: Abonnement;
  nfc?: boolean;
}

export function getUsersStartingWith(
  users: User[],
  prefix: string,
  type: UserField
) {
  return users.filter(user => {
    switch (type) {
      case UserField.Id: {
        return user.id.startsWith(prefix);
      }
      case UserField.Name: {
        return user.name.startsWith(prefix);
      }
      case UserField.Phone: {
        return user.phone.startsWith(prefix);
      }
    }
  });
}

export function generateUserInfo(empty: boolean): UserInfo {
  return {
    sizes: {
      breast: empty ? 0 : randomInt(10, 20),
      hands: empty ? 0 : randomInt(10, 20),
      waist: empty ? 0 : randomInt(10, 20),
      legs: empty ? 0 : randomInt(10, 20)
    },
    weight: empty ? 0 : randomInt(60, 120)
  };
}

export const UsersContainer = createContainer(() => {
  const { appointmentBlocks, updateApps } = AppointmentsInfo.useContainer();

  const [users, setUsers] = useState(Map<string, User>());
  const usersActual = useRef(users);
  usersActual.current = users;

  const addUser = (user: User) =>
    setUsers(usersActual.current.set(user.id, user));
  // const addUsers = (newUsers: User[]) => setUsers(usersActual.current.merge(newUsers.reduce(
  //   (acc: Record<string, User>, user) => ({ ...acc, [user.id]: user }),
  //   {}
  // )));

  const getUser = async (id: string) => {
    if (usersActual.current.has(id)) return usersActual.current.get(id);

    const user = await fetchUser(id, true);

    if (!user) return undefined;

    addUser(user);

    return user;
  };

  const updateUser = useCallback(
    (user: User) => {
      if (!usersActual.current.has(user.id)) addUser(user);

      // throw new Error("attemp to update non-existing user");

      const apps = appointmentBlocks.reduce(
        (acc: List<Appointment>, val) => acc.concat(val.elements),
        List()
      );
      const appsWithUser = apps.filter(app => app.userId === user.id).toArray();

      const updatedApps = appsWithUser.map(app => ({ ...app, user }));

      updateApps(updatedApps);

      console.log(
        `user ${user.name} was updated for ${updatedApps.length} apps`
      );
      setUsers(usersActual.current.set(user.id, user));
      postUser(user);
    },
    [setUsers, updateApps, appointmentBlocks]
  );

  const getUsersStartingWith = useCallback(
    async (prefix: string, type: UserField): Promise<User[]> =>
      await fetchUsersStartingWith(prefix, type),
    []
  );

  return { users, addUser, getUser, updateUser, getUsersStartingWith };
});
