import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

export enum LayoutType {
  Spacious,
  Compact,
  CompactUltra,
}

export const SettingsTopBlockLayoutContainer = createContainer(() => {
  const [layoutTypeStandart, setLayoutTypeStandart] = useCookie(
    "layoutTypeStandart",
    LayoutType.Compact
  );
  const [layoutTypeSticky, setLayoutTypeSticky] = useCookie(
    "layoutTypeSticky",
    LayoutType.CompactUltra
  );

  // const topBlockLayoutSetting : SettingChunk = {

  // }

  return { layoutTypeStandart, layoutTypeSticky };
});
