import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { ColumnsContainer } from './ColumnsContainer';
import { RowsContainer } from './RowsContainer';

export const DraggingAutoScrollContainer = createContainer(() => {
  const { columnWidth } = ColumnsContainer.useContainer();
  const { rowHeight } = RowsContainer.useContainer();
  const [threshold, setThreshold] = useState(0.3);
  const thresholdPixelsHorizontal = columnWidth * threshold;
  const thresholdPixelsVertical = rowHeight * threshold;

  return { thresholdPixelsVertical, thresholdPixelsHorizontal, setThreshold };
});
