import { createContainer } from 'unstated-next';

import { ColumnsContainer } from './ColumnsContainer';
import { OrientationContainer } from './OrientationContainer';
import { RowsContainer } from './RowsContainer';

export const LevelsContainer = createContainer(() => {
  const { horizontal } = OrientationContainer.useContainer();
  const { rowsCount } = RowsContainer.useContainer();
  const { columnsCount } = ColumnsContainer.useContainer();

  const levelsCount = horizontal ? rowsCount : columnsCount;

  return { levelsCount };
});
