import { format, startOfDay } from 'date-fns';
import { Map } from 'immutable';
import { Reducer, useReducer, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../../../assembly/interval';
import { fetchSchedule } from '../../../fetchers/fetchSchedule';

export interface DaySchedule {
  id: number;
  dayInterval: IntervalInt;
  dayDuration: number;
  gridStepDuration: number;
  columnsCount: number;
  restrictedZones: IntervalInt[];
}

export type Schedule = Map<number, DaySchedule>;

export const SchedulesContainer = createContainer(
  (dayScheduleInitital: DaySchedule | undefined) => {
    if (!dayScheduleInitital)
      throw new Error("Start DaySchedule must be provided");

    interface State {
      zeroAxis: number;
      schedule: Schedule;
    }
    enum ActionType {
      AddSchedule,
      SetZeroAxisDaySchedule
    }
    type Action =
      | { type: ActionType.AddSchedule; newDaySchedule: DaySchedule }
      | {
          type: ActionType.SetZeroAxisDaySchedule;
          zeroAxisDaySchedule: DaySchedule;
        };

    const scheduleInitial = (Map() as Schedule).set(
      startOfDay(dayScheduleInitital.dayInterval.start).valueOf(),
      dayScheduleInitital
    );
    const scheduleCurrent = useRef(scheduleInitial);
    const [{ schedule, zeroAxis }, dispatch] = useReducer<
      Reducer<State, Action>
    >(
      (prevState: State, action: Action) => {
        switch (action.type) {
          case ActionType.AddSchedule: {
            const { newDaySchedule } = action;

            scheduleCurrent.current = scheduleCurrent.current.set(
              newDaySchedule.id,
              newDaySchedule
            );
            return {
              zeroAxis: prevState.zeroAxis,
              schedule: scheduleCurrent.current
            };
          }

          case ActionType.SetZeroAxisDaySchedule: {
            const { zeroAxisDaySchedule } = action;
            scheduleCurrent.current = (Map() as Schedule).set(
              zeroAxisDaySchedule.id,
              zeroAxisDaySchedule
            );

            return {
              zeroAxis: zeroAxisDaySchedule.dayInterval.start,
              schedule: scheduleCurrent.current
            };
          }
        }
      },
      {
        zeroAxis: dayScheduleInitital.dayInterval.start,
        schedule: scheduleInitial
      }
    );

    const zeroAxisActual = useRef(zeroAxis);
    zeroAxisActual.current = zeroAxis;

    // const scheduleAllFetched = useRef(scheduleCurrent.current);

    // if (newDaySchedules.count())
    //   dispatch({ type: ActionType.MergeScheduleUpdates });

    // console.log(`zeroAxis: ${format(zeroAxis, "dd HH:mm")}`);

    const setZeroAxisDaySchedule = (daySchedule: DaySchedule) => {
      // if (scheduleCurrent.current.has(daySchedule.id)) return;

      dispatch({
        type: ActionType.SetZeroAxisDaySchedule,
        zeroAxisDaySchedule: daySchedule
      });
    };

    const addDaySchedule = (daySchedule: DaySchedule) => {
      if (scheduleCurrent.current.has(daySchedule.id)) return;

      dispatch({ type: ActionType.AddSchedule, newDaySchedule: daySchedule });
    };

    const jumpToDay = async (dateRaw: Date | number) => {
      console.log(`jump to ${format(dateRaw, "dd")}`);
      const daySchedule = await fetchSchedule(dateRaw);
      // scheduleAllFetched.current = scheduleAllFetched.current.set(
      //   daySchedule.id,
      //   daySchedule
      // );

      setZeroAxisDaySchedule(daySchedule);
      // dispatch({
      //   type: ActionType.SetZeroAxisDaySchedule,
      //   zeroAxisDaySchedule: daySchedule
      // });
    };

    const scheduleWithDay = async (
      date: Date | number,
      scheduleCurrent: Schedule
    ): Promise<Schedule> => {
      const dayId = startOfDay(date).valueOf();
      // const scheduleMerged = scheduleActual.current.merge(scheduleCurrent);
      // console.log(`request ${format(date, "dd")}: ${dayId}`);
      // console.log("exists:", scheduleMerged.has(dayId));
      if (scheduleCurrent.has(dayId)) return scheduleCurrent;
      // if (scheduleAllFetched.current.has(dayId)) {
      //   console.log("------------- cached");
      //   const dayScheduleCached = scheduleAllFetched.current.get(
      //     dayId
      //   ) as DaySchedule;
      //   return scheduleCurrent.set(dayScheduleCached.id, dayScheduleCached);
      // }

      const daySchedule = await fetchSchedule(date);
      // scheduleAllFetched.current = scheduleAllFetched.current.set(
      //   daySchedule.id,
      //   daySchedule
      // );
      // console.log(`fetched ${daySchedule.id}`);
      // console.log("exists:", scheduleMerged.has(dayId));
      // console.log("setting...");
      const newSchedule = scheduleCurrent.set(daySchedule.id, daySchedule);
      addDaySchedule(daySchedule);
      // dispatch({ type: ActionType.AddSchedule, newDaySchedules: newSchedule });

      return newSchedule;
    };

    const getDaySchedule = async (
      dateRaw: Date | number
    ): Promise<DaySchedule> => {
      const dayId = startOfDay(dateRaw).valueOf();
      if (scheduleCurrent.current.has(dayId))
        return scheduleCurrent.current.get(dayId) as DaySchedule;
      // if (scheduleAllFetched.current.has(dayId)) {
      //   console.log("------------- cached");
      //   const dayScheduleCached = scheduleAllFetched.current.get(
      //     dayId
      //   ) as DaySchedule;
      //   return dayScheduleCached;
      // }

      const daySchedule = await fetchSchedule(dateRaw);
      addDaySchedule(daySchedule);
      // if (scheduleAllFetched.current.has(dayId))
      //   console.log("------------- could be cached");

      // scheduleAllFetched.current = scheduleAllFetched.current.set(
      //   daySchedule.id,
      //   daySchedule
      // );

      // dispatch({
      //   type: ActionType.AddSchedule,
      //   newDaySchedules: newDaySchedules.set(daySchedule.id, daySchedule)
      // });

      return daySchedule;
    };

    // console.log(
    //   schedule
    //     .toArray()
    //     .map(([key, value]) => [format(key, "dd HH:mm"), value])
    //     .map(([key]) => key)
    // );

    return {
      schedule,
      getDaySchedule,
      scheduleWithDay,
      // setNewSchedule,
      zeroAxis,
      zeroAxisActual,
      jumpToDay
    };
  }
);
