import './RightPanel.scss';

import classnames from 'classnames';
import React, { useState } from 'react';
import { usePrevious } from 'use-hooks';

import { useKeyPressCode } from '../../../assembly/useKeyPress';
import { SettingsContainer } from '../containers/SettingsContainer';
import SettingBlock from './SettingBlock';

const RightPanel = () => {
  const [opened, setOpened] = useState(false);
  const trigger = useKeyPressCode(192);
  const openedPrevious = usePrevious(opened);

  if (trigger && openedPrevious === opened) setOpened(!opened);

  const { settings } = SettingsContainer.useContainer();

  return (
    <div styleName={classnames("rightPanel", opened ? "opened" : "")}>
      {settings.map(setting => (
        <SettingBlock key={setting.name} setting={setting} />
      ))}
    </div>
  );
};

export default RightPanel;
