import useCookie from '@devhammed/use-cookie';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

import { SettingChunk, SettingType } from './SettingsContainer';

export const SettingsLevelsBlockContainer = createContainer(() => {
  const [visibleLevelsBlock, setVisibleLevelsBlock] = useCookie(
    "visibleLevelsBlock",
    false
  );

  const visibleLevelsBlockSetting: SettingChunk = {
    type: SettingType.Checkbox,
    checked: visibleLevelsBlock,
    name: "Levels block",
    setChecked: setVisibleLevelsBlock,
  };

  return { visibleLevelsBlock, visibleLevelsBlockSetting };
});
