import { addHours, addMinutes, differenceInMilliseconds, format } from 'date-fns';
import { startOfDay } from 'date-fns/esm';

import { IntervalInt } from '../assembly/interval';
import { oneDayDuration } from '../assembly/oneDay';
import { pickRandom, randomInt } from '../assembly/random';
import { DaySchedule } from '../components/Calendar/containers/SchedulesContainer';

const fetchedSchedules = new Map<number, DaySchedule>();

export async function fetchSchedule(
  dateRaw: Date | number
): Promise<DaySchedule> {
  const date = startOfDay(dateRaw).valueOf();

  /* recalculation application (for console)
    let getEnd = (start, step, stepsCount) => start + step * stepsCount
    let calcDD = (start, step, stepsCount) => {
      return {
        start: getHM(start),
        step: getHM(step),
        stepsCount,
        end: getHM(getEnd(start, step, stepsCount))
      }
    }
  */

  const generateRestrictedZone = (
    start: number,
    end: number,
    gridStepDuration: number
  ): IntervalInt => {
    const dayDurationSteps = (end - start) / gridStepDuration;
    const maxDurationSteps = Math.floor(dayDurationSteps * 0.3);

    const zoneStartSteps = randomInt(0, dayDurationSteps - 1);
    const zoneEndSteps = randomInt(
      zoneStartSteps + 1,
      Math.min(zoneStartSteps + maxDurationSteps, dayDurationSteps)
    );

    return {
      start: start + zoneStartSteps * gridStepDuration,
      end: start + zoneEndSteps * gridStepDuration
    };
  };

  const generateScheduleForDay = (
    start: Date,
    end: Date,
    gridStepDuration: number
  ): DaySchedule => {
    const dayDuration = differenceInMilliseconds(end, start);
    return {
      dayInterval: {
        start: start.valueOf(),
        end: end.valueOf()
      },
      dayDuration,
      gridStepDuration,
      columnsCount: dayDuration / gridStepDuration,
      // DISABLED_FUNCTIONAL:
      // restrictedZones: [
      //   generateRestrictedZone(start.valueOf(), end.valueOf(), gridStepDuration)
      // ],
      restrictedZones: [],
      id: startOfDay(start).valueOf()
    };
  };

  const possibleShedules: DaySchedule[] = [
    generateScheduleForDay(
      addMinutes(addHours(date, 9), 30),
      addMinutes(addHours(date, 13), 30),
      oneDayDuration(0, 30)
    ),
    generateScheduleForDay(
      addMinutes(addHours(date, 7), 0),
      addMinutes(addHours(date, 11), 0),
      oneDayDuration(0, 40)
    ),
    generateScheduleForDay(
      addMinutes(addHours(date, 7), 40),
      addMinutes(addHours(date, 12), 20),
      oneDayDuration(0, 40)
    ),
    generateScheduleForDay(
      addMinutes(addHours(date, 6), 20),
      addMinutes(addHours(date, 13), 50),
      oneDayDuration(0, 45)
    )
  ];

  if (fetchedSchedules.has(date))
    return fetchedSchedules.get(date) as DaySchedule;

  const schedule = pickRandom(possibleShedules);
  fetchedSchedules.set(date, schedule);

  console.log("fetch", format(date, "dd"));

  // await new Promise(res => setTimeout(() => res(), randomInt(1000, 2000)));

  return schedule;
}
