import { useEffect, useLayoutEffect, useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { IntervalInt } from '../../../assembly/interval';
import { DaysOffsetsContainer } from './DaysOffsetsContainer';
import { ViewIntervalContainer } from './ViewIntevalContainer';

export const ViewIntervalPixelsContainer = createContainer(() => {
  const {
    viewInterval,
    viewIntervalPending,
    zeroAxisIsUpdating
  } = ViewIntervalContainer.useContainer();
  const {
    dateToOffsetSafe: dateToOffset
  } = DaysOffsetsContainer.useContainer();

  const [viewIntervalPixels, setViewIntervalPixels] = useState<IntervalInt>({
    start: 0,
    end: 0
  });
  const [viewIntervalPixelsPending, setViewIntervalPixelsPending] = useState<
    IntervalInt
  >(viewIntervalPixels);

  const viewIntervalPixelsWidth =
    viewIntervalPixels.end - viewIntervalPixels.start;

  const isScrollingInProgress =
    viewIntervalPixels.start !== viewIntervalPixelsPending.start ||
    viewIntervalPixels.end !== viewIntervalPixelsPending.end;

  useLayoutEffect(() => {
    if (zeroAxisIsUpdating) return;

    (async () => {
      // console.log(
      //   `build viewIntervalPixelsNew on ${intervalToString(viewInterval)} `
      // );
      const viewIntervalPixelsNew: IntervalInt = {
        start: await dateToOffset(viewInterval.start),
        end: await dateToOffset(viewInterval.end)
      };
      setViewIntervalPixels(viewIntervalPixelsNew);
    })();
  }, [viewInterval, dateToOffset, zeroAxisIsUpdating]);

  useLayoutEffect(() => {
    if (zeroAxisIsUpdating) return;

    (async () => {
      const viewIntervalPixelsPendingNew: IntervalInt = {
        start: await dateToOffset(viewIntervalPending.start),
        end: await dateToOffset(viewIntervalPending.end)
      };
      setViewIntervalPixelsPending(viewIntervalPixelsPendingNew);
    })();
  }, [viewIntervalPending, dateToOffset, zeroAxisIsUpdating]);

  // if (viewIntervalPixels.start > viewIntervalPixels.end) debugger;

  return {
    viewIntervalPixels,
    viewIntervalPixelsPending,
    isScrollingInProgress,
    viewIntervalPixelsWidth,
    zeroAxisIsUpdating
  };
});
