const Calendar = () => {
  // const { isScrolling, scrollLeft, scrollRight } = ScrollInfo.useContainer();
  // const { setPageLoaded } = UIInfo.useContainer();

  const { scrollLeft, scrollRight } = ScrollControlContainer.useContainer();
  const { vertical } = OrientationContainer.useContainer();
  const { sizePixelsTimeDimension } = WrapperContainer.useContainer();
  const { initialized } = InitializeStateContainer.useContainer();
  const {
    setNewViewIntervalImmediately,
    viewInterval,
    setZeroAxisLinked,
  } = ViewIntervalContainer.useContainer();
  const { offsetToDate, dateToOffset } = DaysOffsetsContainer.useContainer();
  const { zeroAxis } = SchedulesContainer.useContainer();

  const { columnWidth } = ColumnsContainer.useContainer();

  const { width: widthWindow, height: heightWindow } = useWindowSize();
  const widthWindowD = useDebounce(widthWindow, 500);
  const heightWindowD = useDebounce(heightWindow, 500);

  const arrowRightPressed = useKeyPress("ArrowRight", true);
  const arrowLeftPressed = useKeyPress("ArrowLeft", true);

  // console.log({ initialized });

  const handlers = useSwipeable({
    onSwipedLeft: () => scrollLeft(),
    onSwipedRight: () => scrollRight(),
  });

  if (arrowRightPressed) scrollRight();
  else if (arrowLeftPressed) scrollLeft();

  // useEffect(() => {
  //   (async () => {
  //     setPageLoaded(true);
  //   })();

  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);

  useLayoutEffect(() => {
    if (!initialized) return;

    (async () => {
      const start = await offsetToDate(0);
      const end = await offsetToDate(sizePixelsTimeDimension);

      const viewIntervalInitial: IntervalInt = {
        start,
        end,
      };

      console.log("viewIntervalInitial:", viewIntervalInitial);

      setNewViewIntervalImmediately(viewIntervalInitial);
      setZeroAxisLinked(zeroAxis);
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialized, zeroAxis]);

  // const timeWindowDimension = vertical ? heightWindowD : widthWindowD;

  // useLayoutEffect(() => {
  //   if (!initialized) return;

  //   (async () => {
  //     const { start } = viewInterval;
  //     const end = await offsetToDate(
  //       (await dateToOffset(start)) + sizePixelsTimeDimension
  //     );
  //     // const end = start;
  //     const viewIntervalInitial: IntervalInt = {
  //       start,
  //       end
  //     };
  //     // console.log({ viewIntervalInitial });
  //     setNewViewIntervalImmediately(viewIntervalInitial);
  //   })();

  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [timeWindowDimension, columnWidth]);

  // const topBlockRef = useRef<HTMLDivElement>(null);
  // const [bottomBlockOffsetTop, setBottomBlockOffsetTop] = useState(0);
  // useEffect(() => {
  //   const topBlock = topBlockRef.current;

  //   if (!topBlock) return;

  //   setBottomBlockOffsetTop(topBlock.offsetTop + topBlock.offsetHeight);
  // }, []);

  return useMemo(
    () => (
      <>
        <AutoFetcherApps.Provider>
          <AutoFetcherEvents.Provider>
            <LevelBlocksContainer.Provider>
              <ActiveAppContainer.Provider>
                <div styleName="calendar" {...handlers}>
                  {/* <TopBlock ref={topBlockRef} /> */}
                  <TopBlock />
                  <NewAppContainer.Provider>
                    <BottomBlock />
                    {/* <AppEditor offsetTop={bottomBlockOffsetTop} /> */}
                  </NewAppContainer.Provider>
                  {/* <div styleName="overlookingRightFrame" />
                  <div styleName="overlookingLeftFrame" /> */}
                </div>
              </ActiveAppContainer.Provider>
            </LevelBlocksContainer.Provider>
          </AutoFetcherEvents.Provider>
        </AutoFetcherApps.Provider>
      </>
    ),
    [handlers]
  );
};

const WrapperSettings = () => {
  return (
    <SettingsAppContainer.Provider>
      <SettingsColumnContainer.Provider>
        <SettingsDraggingContainer.Provider>
          <SettingsBackGridContainer.Provider>
            <SettingsLevelsBlockContainer.Provider>
              <SettingsDividersContainer.Provider>
                <SettingsAddAppButtonContainer.Provider>
                  <SettingsTopBlockLayoutContainer.Provider>
                    <SettingsContainer.Provider>
                      <Calendar />
                      <RightPanel />
                    </SettingsContainer.Provider>
                  </SettingsTopBlockLayoutContainer.Provider>
                </SettingsAddAppButtonContainer.Provider>
              </SettingsDividersContainer.Provider>
            </SettingsLevelsBlockContainer.Provider>
          </SettingsBackGridContainer.Provider>
        </SettingsDraggingContainer.Provider>
      </SettingsColumnContainer.Provider>
    </SettingsAppContainer.Provider>
  );
};

const WrapperProviders = () => {
  return (
    <DraggingContainer.Provider>
      {/* <GridInfo.Provider> */}
      <DaysOffsetsContainer.Provider>
        <LevelsContainer.Provider>
          {/* <LeftColumnInfo.Provider> */}
          <AppointmentsInfo.Provider initialState={{ blockSize: 10 }}>
            <EventsContainer.Provider>
              <UsersContainer.Provider>
                <UIScrollStateContainer.Provider>
                  <ViewIntervalContainer.Provider>
                    <ViewIntervalPixelsContainer.Provider>
                      <ScrollControlContainer.Provider>
                        <ViewIntervalBufferedContainer.Provider>
                          <WrapperSettings />
                        </ViewIntervalBufferedContainer.Provider>
                      </ScrollControlContainer.Provider>
                    </ViewIntervalPixelsContainer.Provider>
                  </ViewIntervalContainer.Provider>
                </UIScrollStateContainer.Provider>
              </UsersContainer.Provider>
            </EventsContainer.Provider>
          </AppointmentsInfo.Provider>
          {/* </LeftColumnInfo.Provider> */}
        </LevelsContainer.Provider>
      </DaysOffsetsContainer.Provider>
      {/* </GridInfo.Provider> */}
    </DraggingContainer.Provider>
  );
};

const WrapperInitial = () => {
  const [scheduleForCurrentDay, setScheduleForCurrentDay] = useState<
    DaySchedule | undefined
  >();

  useEffect(() => {
    fetchSchedule(startOfDay(Date.now()).valueOf()).then((schedule) =>
      setScheduleForCurrentDay(schedule)
    );
  }, []);

  return !scheduleForCurrentDay ? null : (
    <OrientationContainer.Provider>
      <WrapperContainer.Provider>
        <ColumnsContainer.Provider>
          <RowsContainer.Provider>
            <InitializeStateContainer.Provider>
              <SchedulesContainer.Provider initialState={scheduleForCurrentDay}>
                <WrapperProviders />
              </SchedulesContainer.Provider>
            </InitializeStateContainer.Provider>
          </RowsContainer.Provider>
        </ColumnsContainer.Provider>
      </WrapperContainer.Provider>
    </OrientationContainer.Provider>
  );
};

