import './InputText.scss';

import classnames from 'classnames';
import React, { InputHTMLAttributes } from 'react';

import InputWrapper from '../InputWrapper';

const InputText = (
  attrs: InputHTMLAttributes<any> & {
    wrapper: "span" | "div";
    invalid?: boolean;
  }
) => {
  const { wrapper, invalid, ...inputProps } = attrs;
  return (
    <InputWrapper wrapper={wrapper}>
      <input
        type="text"
        styleName={classnames("inputText", invalid ? "invalid" : "")}
        {...inputProps}
      />
    </InputWrapper>
  );
};

export default InputText;
