import './EventsPlacer.scss';

import React, { useCallback, useMemo } from 'react';

import { areIntervalsIntersects } from '../../../../../assembly/interval';
import { Event, EventsContainer } from '../../../../../stores/events';
import { DaysOffsetsContainer } from '../../../containers/DaysOffsetsContainer';
import { LevelBlocksContainer } from '../../../containers/LevelBlocksContainer';
import { RowsContainer } from '../../../containers/RowsContainer';
import { SettingsDividersContainer } from '../../../containers/SettingsDivideresContainer';
import { ViewIntervalBufferedContainer } from '../../../containers/ViewIntervalBufferedContainer';
import EventCell from './EventCell';

const EventsPlacer = () => {
  const { events } = EventsContainer.useContainer();
  const { dateToOffset } = DaysOffsetsContainer.useContainer();
  const { levelBlocks } = LevelBlocksContainer.useContainer();
  const { rowHeight } = RowsContainer.useContainer();
  const { viewIntervalBuffered } = ViewIntervalBufferedContainer.useContainer();
  const { dividerUnderBlock } = SettingsDividersContainer.useContainer();

  // useWhyDidYouUpdate("events placer", {
  //   viewIntervalBuffered,
  //   events,
  //   dateToOffset,
  //   rowHeight,
  //   levelBlocks
  // });

  const getInfo = useCallback(
    (event: Event): { x: number; y: number; width: number; height: number } => {
      const levelBlocksPast = Math.max(
        levelBlocks.filter(
          block => (dividerUnderBlock ? block.end : block.start) <= event.block
        ).length,
        dividerUnderBlock ? 0 : 1
      );

      const x = dateToOffset(event.interval.start);
      const y = (event.block + levelBlocksPast) * rowHeight;

      const endX = dateToOffset(event.interval.end);
      const width = endX - x;
      const height = rowHeight;

      return { x, y, width, height };
    },
    [dateToOffset, rowHeight, levelBlocks, dividerUnderBlock]
  );

  const eventsInRange = useMemo(
    () =>
      events
        .valueSeq()
        .filter(event =>
          areIntervalsIntersects(event.interval, viewIntervalBuffered)
        ),
    [events, viewIntervalBuffered]
  );

  const eventsJSX = useMemo(
    () =>
      eventsInRange
        .map(event => {
          const info = getInfo(event);
          return <EventCell key={event.id} event={event} {...info} />;
        })
        .toArray(),
    [getInfo, eventsInRange]
  );

  return useMemo(() => <>{eventsJSX}</>, [eventsJSX]);
};

export default EventsPlacer;
