import './InputWrapper.scss';

import React from 'react';

const InputWrapper = ({
  children,
  wrapper
}: React.Props<any> & { wrapper: "span" | "div" }) => {
  return wrapper === "span" ? (
    <span styleName="inputWrapper">{children}</span>
  ) : (
    <div styleName="inputWrapper">{children}</div>
  );
};

export default InputWrapper;
