import './RestrictedZone.scss';

import React from 'react';

export interface RestrictedZoneProps {
  width: number;
  height: number;
  x: number;
  y: number;
  mark: string;
}

const RestrictedZone = ({ width, height, x, y, mark }: RestrictedZoneProps) => {
  return (
    <div styleName="restrictedZone" style={{ width, height, left: x, top: y }}>
      {mark}
    </div>
  );
};

export default React.memo(RestrictedZone);
