import React, { useMemo, useRef } from 'react';

import { AppointmentNoUser, AppointmentsBlock } from '../../../../../../stores/appointments';
import { LevelBlock } from '../../../../containers/LevelBlocksContainer';
import { SettingsDividersContainer } from '../../../../containers/SettingsDivideresContainer';
import AppointmentWrapper from '../AppointmentWrapper';

export interface AppointmentBlockProps {
  appsBlock: AppointmentsBlock;
  rowHeight: number;
  columnWidth: number;
  // zeroAxis: Date | number;
  gridStepDuration: Date | number;
  // dayDuration: number;
  // startOffset: number;
  levelBlocks: LevelBlock[];
  dateToOffset: (date: Date | number) => number;
}

export function generateAppForced(
  app: AppointmentNoUser,
  width: number,
  height: number,
  dateToOffset: (date: Date | number) => number,
  levelBlocks: LevelBlock[],
  rowHeight: number,
  dividerUnderBlock: boolean
) {
  const levelBlocksPast = Math.max(
    levelBlocks.filter(
      block => (dividerUnderBlock ? block.end : block.start) <= app.rowIndex
    ).length,
    dividerUnderBlock ? 0 : 1
  );

  const x = dateToOffset(app.interval.start);
  const y = (app.rowIndex + levelBlocksPast) * rowHeight;

  return (
    <AppointmentWrapper
      key={app.id}
      height={height}
      width={width}
      left={x}
      top={y}
      app={app}
    />
  );
}

export function generateApp({
  app,
  rowHeight,
  columnWidth,
  gridStepDuration,
  levelBlocks,
  dateToOffset,
  dividerUnderBlock
}: {
  app: AppointmentNoUser;
  rowHeight: number;
  columnWidth: number;
  gridStepDuration: number | Date;
  levelBlocks: LevelBlock[];
  dateToOffset: (date: Date | number) => number;
  dividerUnderBlock: boolean;
}): JSX.Element {
  const appDuration = app.interval.end - app.interval.start;

  const height = rowHeight;
  const width = columnWidth * (appDuration / gridStepDuration.valueOf());

  return generateAppForced(
    app,
    width,
    height,
    dateToOffset,
    levelBlocks,
    rowHeight,
    dividerUnderBlock
  );
}

// const AppointmentBlockInternal =

const AppointmentBlock = React.memo(
  ({
    appsBlock,
    rowHeight,
    gridStepDuration,
    columnWidth,
    // zeroAxis,
    // dayDuration,
    // startOffset,
    dateToOffset,
    levelBlocks
  }: AppointmentBlockProps) => {
    const { dividerUnderBlock } = SettingsDividersContainer.useContainer();

    //  console.log("app block updated");
    const cachedApps = useRef<Record<string, JSX.Element>>({});

    const cachedByEnvironment = useMemo(
      () =>
        appsBlock.elements.reduce(
          (acc: Record<string, JSX.Element>, app) => ({
            ...acc,
            [app.id]: generateApp({
              app,
              columnWidth,
              gridStepDuration,
              rowHeight,
              // zeroAxis,
              // dayDuration,
              // startOffset,
              dateToOffset,
              levelBlocks,
              dividerUnderBlock
            })
          }),
          {}
        ),
      [
        rowHeight,
        gridStepDuration,
        columnWidth,
        // zeroAxis,
        appsBlock.elements,
        // dayDuration,
        // startOffset,
        dateToOffset,
        levelBlocks,
        dividerUnderBlock
      ]
    );

    const appElements = appsBlock.elements.reduce(
      (acc: Record<string, JSX.Element>, app) => ({
        ...acc,
        [app.id]:
          cachedByEnvironment[app.id] ||
          cachedApps.current[app.id] ||
          generateApp({
            app,
            columnWidth,
            gridStepDuration,
            rowHeight,
            // zeroAxis,
            // dayDuration,
            dateToOffset,
            // startOffset,
            levelBlocks,
            dividerUnderBlock
          })
      }),
      {}
    );

    cachedApps.current = appElements;

    return <>{Object.values(appElements)}</>;
  }
);

export default AppointmentBlock;
