import './index.scss';

import React from 'react';
import { render } from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import Calendar from './components/Calendar';
import Navigation from './components/Navigation';
import RightBar from './components/RightBar';
import TestPage from './components/TestPage';
import TestPage2 from './components/TestPage2';
import VersionBox from './components/VersionBox';
import { register } from './serviceWorker';
import { LazyTaskContainer } from './stores/lazytask';

// here we disable console and performance for better production experience
console.log(process.env.NODE_ENV);
if (!process || !process.env || process.env.NODE_ENV !== "development") {
  performance.mark = () => undefined as any;
  performance.measure = () => undefined as any;
  console.log = () => undefined as any;
}

const App = () => {
  return React.useMemo(
    () => (
      <>
        <Router>
          <Navigation />
          <Switch>
            <Route exact path="/">
              <LazyTaskContainer.Provider>
                <Calendar />
              </LazyTaskContainer.Provider>
            </Route>
            <Route path="/testPage">
              <TestPage />
            </Route>
            <Route path="/testPage2">
              <TestPage2 />
            </Route>
          </Switch>
          <RightBar />
        </Router>
        <VersionBox />
        <div
          id="appDraggablePortal"
          // style={{ position: "absolute", left: 0, top: 0 }}
        />
      </>
    ),
    []
  );
};

render(<App />, document.getElementById("root"));

register();
