import './Button.scss';

import classnames from 'classnames';
import React, { ButtonHTMLAttributes } from 'react';

import InputWrapper from '../InputWrapper';

const Button = (
  attrs: ButtonHTMLAttributes<any> & {
    wrapper: "span" | "div";
    danger?: boolean;
  }
) => {
  const { wrapper, danger, ...inputProps } = attrs;

  return (
    <InputWrapper wrapper={wrapper}>
      <button
        styleName={classnames("button", danger ? "danger" : "")}
        {...inputProps}
      >
        {attrs.children}
      </button>
    </InputWrapper>
  );
};

export default Button;
