import './MonthChunkToday.scss';

import React, { useCallback, useMemo } from 'react';

import { SchedulesContainer } from '../../containers/SchedulesContainer';
import MonthChunk from '../MonthRow/MonthChunk';

const MonthChunkToday = () => {
  const { jumpToDay } = SchedulesContainer.useContainer();
  const setCurrentDay = useCallback((date: number) => jumpToDay(date), [
    jumpToDay
  ]);

  return useMemo(
    () => (
      <div styleName="monthChunkToday">
        <MonthChunk
          appsCount={169}
          date={Date.now()}
          setCurrentDate={setCurrentDay}
          todayMode
        />
      </div>
    ),
    [setCurrentDay]
  );
};

export default MonthChunkToday;
